'use strict';

/**
 * @ngdoc directive
 * @name helpPanel
 * @description Displays product version and environment info
 */
// @ngInject
module.exports = {
  controllerAs: 'vm',
  // @ngInject
  controller: function ($scope, $window, store, serviceUrl,api,flash,$compile) {
    this.helpContent = "Help content";
    $scope.$watch(
      function () {
        return store.frames();
      },
      function (frames) {
        if (frames.length === 0) {
          return;
        }
        //this.url = frames[0].uri;
        //this.documentFingerprint = frames[0].metadata.documentFingerprint;
        api.help.getContent({}).then(function(resp) {
    		var helpContent = resp.message;
    		$compile(helpContent)($scope);
    		angular.element(document.querySelector('.help-panel-content')).html(helpContent);
    	}.bind(this)).catch(function (err) {
			flash.error(err.message, 'Get help content failed',{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
		});
      }.bind(this)
    );
  },
  template: require('../templates/help-panel.html'),
  bindings: {
    auth: '<',
    onClose: '&',
  },
};
