'use strict';

/**
 * A service for displaying "confirm"  messages for Removing document from the task space.
 */

// @ngInject
function ConfirmDocRemoveAction($modal,groups,$q) {
	var self = this;
	var modalOptions = {
		selectedGroup: {},
		closeButtonText: 'Cancel',
		moveButtonText: 'Remove',
		headerText: 'Are you sure you want to remove this document from task space?',
	};
	self.modalInstance = null;
	
	self.removeConfimation = function(fromGroup) {
		let link = groups.checkDocuLinkExists(fromGroup).then(function(link) {
			return link;
		});

		let annotCount = groups.getAnnotationCount(fromGroup).then(function(count) {
			return count;
		});

		return $q.all([link, annotCount]).then(function(resp) {
			let data = {};
			if (resp && resp.length > 0) {
				data = {
					link: resp[0],
					count: resp[1],
				};
			}
			return self.confirm(data,fromGroup);
		});
	};
	
	
	self.confirm = function (confirmdata,fromGroup) {
		  if(this.modalInstance) {
			this.modalInstance.dismiss("cancel");
			this.modalInstance = null;
		  }

	      this.modalInstance = $modal.open({
				  animation: true,
				  template: require('../templates/confirm-docremove-action.html'),
				  controller: 'DocRemoveConfirmModal',
				  controllerAs: 'vm',
				  backdrop: 'static',
				  size : 'lg',
				  resolve: {
					  confirmdata :function() {
						  return confirmdata;
					  },
			          modalOptions: function() {
					  	  return modalOptions;
					  },
			          taskspace: function() {
						  return fromGroup;
					  }
				  }
			});
			return this.modalInstance.result;
	};
}

module.exports = ConfirmDocRemoveAction;