'use strict';

/* @ngInject */
function TagInfoDialogController($scope, api, groups, flash,store) {
   'ngInject';
	var self = this;

	self.tags = [];

	self.tab = 1;
	self.changedTags = null;
	self.enableSave = false;
	self.setTab = function(newTab)  {
		self.tab = newTab;
	};

	self.isSet = function(tabNum)  {
		return self.tab === tabNum;
	};

	self.onTagsEdited = function(tags) {
		self.changedTags = tags;
		var status = false;
		if (self.changedTags) {
			self.changedTags.forEach(function(item) {
				if (item.isNew) {
					status = true;
					return false;
				}
			});
		} 
		self.enableSave = status;
	};

	self.untag = function(tag) {
		var group = groups.focused();
		if (group && group.objId && group.objClientId) {
			let postdata = {
				"tagId": tag.tagId,
				"topObjectId": group.objId,
				"topObjectClientId": group.objClientId
			};
			api.tags.untag({}, postdata).then(function(tags) {
				if (tags) {
					self.changedTags = self.changedTags.filter(function(item) {
						if (item.tagId != postdata.tagId) {
							return item;
						}
					});
					
					self.tags = self.tags.filter(function(item) {
						if (item.TagId != postdata.tagId) {
							return item;
						}
					});
				}
			});
		}
	}

	function getTagsPostData() {
		let items, tags;
		var group = groups.focused();
		if (group && group.objId && group.objClientId) {
			items = [
				{
					"Type": "Document",
					"TopObjectId": group.objId,
					"TopObjectClientId": group.objClientId
				}
			];
		}

		if (self.changedTags) {
			tags = self.changedTags.filter(function(item) {
				if (item.isNew) {
					return item;
				}
			});
		}
		return {
			"Items": items,
			"tags": tags
		};
	}

	self.saveTags = function() {
		var group = groups.focused();
		if (group && group.objId && group.objClientId) {
			let postdata = getTagsPostData();
			if (postdata && postdata.Items && postdata.tags)
				api.tags.tag({}, postdata).then(function(tags) {
					if (tags && tags.resultList && tags.resultList.length > 0) {
						let tagObj =  tags.resultList[0];
						if(!tagObj.isTagged) {
							if(tagObj.Reason == "NoPermOnObject"){
								flash.error("You Dont Have Permissions To Tag This Document");
							} else {
								flash.error("Unable to Add the tag");
							}
						} else {
							flash.success("Document tagged successfully");
							init();
							self.cancel();
						}
					}
				});
		}
	};
	
	self.cancel = function() {
		store.selectTagBtn(false);
		self.onClose();
	};

	$scope.$watch(() => ([groups.focusedId()]), ([currentGroupId], [prevGroupId]) => {
		if (currentGroupId !== prevGroupId) {
			init();
		}
	}, true);

    $scope.$on('$destroy', function() {
		store.selectTagBtn(false);
	});

	function getTagInfo(objectId, objectClientId) {
		api.tags.getResourceTags({}, { "objectType": "high", "objectId": objectId, "objectClientId": objectClientId }).then(function(tags) {
			if (tags) {
				self.tags = tags.Tag || [];
			}
		});
	}

	function init() {
		self.changedTags = null;
		self.enableSave = false;
		var group = groups.focused();
		if (group && group.objId && group.objClientId) {
			getTagInfo(group.objId, group.objClientId);
		}
	}
	init();
}

module.exports = {
	controller: TagInfoDialogController,
	controllerAs: 'vm',
	bindings: {
		onClose: '&',
		editMode: '=',
	},
	template: require('../templates/tags-info-dialog.html'),
};