'use strict';
var events = require('../events');
// @ngInject
function SelectSectionController($scope,groups,api,userPreference) {
  'ngInject';
  const disabledTitile = "Web page has already been added to a section. Use Move button to move the web page to a different section";
  var self= this;
  self.groups = groups; 
  self.sections = null;
  var noSection = {
	name: "< No Section >",
	id: null
  };
 
  self.allowCreateSection = userPreference.canCreateSection;
  self.getGroup = function() {
	let grp = angular.copy(self.group);
	//let grp = Object.assign({}, self.group);
	if(!grp.sections) {
		grp.sections = [];
	}
	let sections = grp.sections;
	if(!sections.find((sec) => { sec.id == null; })) {
		sections.unshift(noSection);
	    grp.sections = sections;
	}
	return grp;
  };

  self.focusSection = function (section) {
	if(self.group && self.storeSection) {
		self.storeSection({
	      groupId: self.group.id,
	      section: section,
	    });
        groups.savefocusSection(self.group,section);
	}
	self.section = section;
  };

  self.unfocusSection = function() {
	if(self.group && self.storeSection) {
		self.storeSection({
	      groupId: self.group.id,
	      section: noSection,
	    });
        groups.savefocusSection(self.group,noSection);
	}
  };

  
  
  this.unselectSection = function($event) {
	self.section = Object.assign({}, noSection);
	self.selected = {
		"section" : self.section
	};
	self.focusSection(noSection);
  };

  this.getDisabledTitile = function(selected) {
	if(self.selectDisabled) {
		return disabledTitile;
	} else if(selected) {
		return selected;
	}
  };
  
  $scope.$watch(angular.bind(self, function () {
	  return self.section;
  }), function (newSection, oldSection) {
	  self.section = newSection;
      self.selected = {
		"section" : self.section || noSection
	  };
  });

  $scope.$watch(angular.bind(self, function () {
	  return self.group;
  }), function (newgroup, oldgroup) {
	  init();
  },true);

  function init() {
	self.sections = null;
	let grp = angular.copy(self.group);
	//let grp = Object.assign({}, self.group);
	if(!grp.sections) {
		grp.sections = [];
	}
	let sections = grp.sections;
	if(!sections.find((sec) => { sec.id == null; })) {
		sections.unshift(noSection);
	}
    self.sections = sections;
  }

  init();

  /*self.$onInit = function() {
      self.selected = {
		"section" : self.section
	  };
  };

  self.$doCheck = function(changes) {
      self.selected = {
		"section" : self.section
	  };
  };
*/
 /* $scope.$watch(() => ([groups.focusedId()]), ([currentGroupId], [prevGroupId]) => {
	if (currentGroupId !== prevGroupId) {
		self.group = groups.focused();
	}
  }, true);

  $scope.$on(events.GROUP_UPDATED,function(){
	self.group = groups.focused();
  });*/

}

module.exports = {
  controller: SelectSectionController,
  controllerAs: 'vm',
  bindings: {
    auth: '<',
    group: '<',
    storeSection: '&',
    section: '=?',
    onNewSection:'&',
    appendToBody:'<',
    selectDisabled: '<',
  },
  template: require('../templates/select-section.html'),
};


