'use strict';
var events = require('../events');

/**
 * A service for displaying "confirm"  messages.
 */

// @ngInject
function AddSectionModalController($scope,toGroup,modalOptions,$modalInstance,api,groups,flash) {
	var self = this;
	self.modalOptions = modalOptions;
	self.group = toGroup;
	self.section = {};
	
	self.ok = function () {
		api.group.addSection({},self.section).then(function(resp) {
			if(resp && resp.TsSection) {
				groups.addSection(self.section.taskspaceId ,resp.TsSection);
				groups.savefocusSection(self.group,resp.TsSection);
				$modalInstance.close(resp.TsSection);
			}
		}).catch(function (err) {
	      	flash.error(err.message,{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
	    });;
  	};

    $scope.$on(events.USER_CHANGED, function () {
		self.cancel();
	});
	
  	self.cancel = function () {
    	$modalInstance.dismiss('cancel');
  	};

    function init() {
		self.section.clientId = self.group.clientId;
	    self.section.taskspaceId = self.group.id;
    }
	
	init();
}
module.exports = AddSectionModalController;