/**
 * @ngdoc service
 * @name  groups
 *
 * @description Provides access to the list of groups that the user is currently
 *              a member of and the currently selected group in the UI.
 *
 *              The list of groups is initialized from the session state
 *              and can then later be updated using the add() and remove()
 *              methods.
 */
'use strict';
var kyes = require('../keys');

var STORAGE_KEY = 'hypothesis.groups.focus';

var events = require('../events');
var { awaitStateChange } = require('../util/state-util');
var serviceConfig = require('../service-config');

function updateGroupmodel(state) {
  state = state || {};
  return Object.assign({}, {
    // Apply changes from the draft
   id: state.id || null,
   stateKey: kyes.GROUPS_UI_KEY,
   stateValue: state.groupId,
  });
}

// @ngInject
function groups($rootScope, store, api, isSidebar, localStorage, settings, $filter, frameSync, flash, $q, _,$modal,userstate) {
	var self = this;
	var documentUri;
	var docMetaInfo;
	var userUIState;
	var svc = serviceConfig(settings);
	var authority = svc ? svc.authority : null;

	var openGroupdropDown = false;
	
	var modalOptions = {
	    close: 'CANCEL',
	    ok: 'CREATE',
	    headerText: 'Create a New Taskspace'
	};
    var createmodalInstance = null;


    function getGroupState(params) {
	    var postdata = updateGroupmodel(params || {});
		return api.profile.getUserState({},postdata);
	}
	
	function saveGroupState(params) {
		var postdata = updateGroupmodel(params);
		return api.profile.saveUserState({},postdata);
	}

	function getDocumentUriForGroupSearch() {
		function mainUri() {
			var uris = store.searchUris();
			if (uris.length === 0) {
				return null;
			}

			// We get the first HTTP URL here on the assumption that group scopes must
			// be domains (+paths)? and therefore we need to look up groups based on
			// HTTP URLs (so eg. we cannot use a "file:" URL or PDF fingerprint).
			return uris.find(uri => uri.startsWith('http'));
		}
		return awaitStateChange(store, mainUri);
	}


	function hasGroup(gs, id) {
		var status = null;
		if (gs && gs.length > 0) {
			for (var i = 0; i < gs.length; i++) {
				if (gs[i].id == id) {
					status = gs[i];
					break;
				}
			}
		}
		return status;
	}

    function getValidGroup(gs) {
		var status = null;
		if (gs && gs.length > 0) {
			for (var i = 0; i < gs.length; i++) {
				if (isSelectable(gs[i])) {
					status = gs[i];
					break;
				}
			}
		}
		return status;
	}

	/**
	 * Fetch the list of applicable groups from the API.
	 *
	 * The list of applicable groups depends on the current userid and the URI of
	 * the attached frames.
	 */
	function load(groupId) {
		var uri = Promise.resolve(null);
		var params = {
				expand: 'organization',
		};
		if (isSidebar) {
			uri = getDocumentUriForGroupSearch();
		}
		return uri.then(uri => {
			
			if (authority) {
				params.authority = authority;
			}
			if (uri) {
				params.document_uri = uri;
			}

			if (settings.group) {
				params.tsId = settings.group;
			}
			
			/*
			if(settings.client) {
				params.tsClientId = settings.client;
			}*/
            var usrstate = Promise.resolve(null);
			if (store.profile() && store.profile().userid && !store.profile().isPublic) {
				usrstate = getGroupState();
				//console.log("from getGroupState");
			} else {
				//console.log("Not from getGroupState");
			}
			return usrstate;
			
		}).then(us => {
			userUIState = us;
			return api.groups.list(params);
		}).then(gs => {
			return loadAllGroups(gs,groupId);
		});
	}

    function loadAllGroups(gs,groupId) {
			var prevFocusedGroup,validGroup,defaultGroup,openGroupdropDown = false;
			var isFirstLoad = store.allGroups().length === 0;
			if(gs && gs.length == 0) {
				return;
			} 
			store.loadGroups(gs);
				
			if(userUIState && userUIState.stateKey == kyes.GROUPS_UI_KEY) {
				prevFocusedGroup = userUIState.stateValue;
				//console.log("from getGroupState groupId "+ prevFocusedGroup);
			}
			
			if (groupId) {
				prevFocusedGroup = groupId;
			} else if (settings.group) {
				prevFocusedGroup = settings.group;
			} else if (gs && gs.length > 0) {
				var groupSortedByCount = $filter('orderBy')(gs, '-annotationCount');
				defaultGroup = getValidGroup(groupSortedByCount);
				if(prevFocusedGroup == null || prevFocusedGroup == "null") {
					validGroup = defaultGroup;
				} else {
					var groupWithCount = $filter('filter')(groupSortedByCount, function(item) {
						return item.annotationCount > 0;
					});

					if (groupWithCount.length > 0) {
						var currentGroupWithCount = $filter('filter')(groupWithCount, { 'id': prevFocusedGroup });
						if (currentGroupWithCount.length > 0) {
							validGroup = getValidGroup(currentGroupWithCount);
						} else {
							validGroup = getValidGroup(groupWithCount);
							openGroupdropDown = true;
						}
					} else {
						var currentGroupWithWebDoc = $filter('filter')(groupSortedByCount, function(item) {
							return item.objId ? item.objId : null;
						});

						if (currentGroupWithWebDoc.length > 0) {
							validGroup = getValidGroup(currentGroupWithWebDoc);
						}
					}
				}
			}

			prevFocusedGroup = validGroup ? validGroup.id : prevFocusedGroup;
			
			if ((isFirstLoad || groupId) && gs.length > 0) {
				var gp = hasGroup(gs, prevFocusedGroup);
				if (gp && isSelectable(gp)) {
					store.focusGroup(prevFocusedGroup);
					//console.log("focused group id"+ prevFocusedGroup + " name "+gp.name);
				} else if(defaultGroup) {
					prevFocusedGroup = defaultGroup.id;
					store.focusGroup(prevFocusedGroup);
					//console.log("focused defaultGroup"+ prevFocusedGroup + " name "+defaultGroup.name);
				} else {
					prevFocusedGroup = null;
					if(groupId || settings.group) {
						flash.error('You do not have permission to view this Taskspace. Please select Taskspace.',{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
					}
				}
			}
			
			if(prevFocusedGroup) {
				let group =  focused(groupId);
    		    let section = getDocSection(group);
    		    focusSection(groupId,section);
			}
			//store.loadGroups(gs,prevFocusedGroup);
			//console.log("focused group "+ prevFocusedGroup);
			return store.allGroups();
    }
    
	function updateCount(countMap) {
		for (var key in countMap) {
			if (countMap.hasOwnProperty(key)) {
				store.updateCount(key, countMap[key]);
			}
		}
	}

	function updateGroup(id, count,webResource) {
		store.updateGroup(id, count,webResource);
	}

	function all() {
		return store.allGroups();
	}

	// Return the full object for the group with the given id.
	function get(id) {
		return store.getGroup(id);
	}

	/**
	 * Leave the group with the given ID.
	 * Returns a promise which resolves when the action completes.
	 */
	function leave(id) {
		// The groups list will be updated in response to a session state
		// change notification from the server. We could improve the UX here
		// by optimistically updating the session state
		return api.group.member.delete({
			pubid: id,
			user: 'me',
		});
	}


	/** Return the currently focused group. If no group is explicitly focused we
	 * will check localStorage to see if we have persisted a focused group from
	 * a previous session. Lastly, we fall back to the first group available.
	 */
	function focused() {
		var gp = store.focusedGroup();
		return gp;
	}
	
	
	/** Return the currently focused section in a given group. 
	 */
	function getfocusedSection(groupId) {
		var section = store.getfocusedSection(groupId);
		return section;
	}

	/** Return the currently focused group id. */
	function focusedId() {
		var group = focused();
		if (group) {
			return group.id;
		}

		return null;
	}

	function getwebResource() {
		var group = focused();
		if (group) {
			return group.webresource;
		}
		return null;
	}
	
	function getPdfbyUrl(group,url) {
		if (!_.isEmpty(group.objects)) {
			return _.find(group.objects, function(doc) { 
				return doc.sourceUrl  == url && "OtherDoc".toUpperCase() == ( doc.docType ? doc.docType.toUpperCase() : null); 
			});
		}
		return null;
	}
	
	/** Set the group with the passed id as the currently focused group. */
	function focus(id) {
		store.focusGroup(id);
	}
	
	/** Set the section with the passed groupid as the currently focused section. */
	function focusSection(groupId,section) {
		store.focusSection(groupId,section);
	}
	/** Adds the section to the group with the passed groupid and sets as the currently focused section. */
	function addSection(groupId,section) {
		store.addSection(groupId,section);
		$rootScope.$broadcast(events.GROUP_UPDATED,groupId);
	}

	function getDocMetaInfo() {
		return docMetaInfo ? Object.assign({}, docMetaInfo) : null;
	}
	
	function fetchDocumentInfo() {
		return frameSync.fetchDocumentInfo();
	}
	
	function getMetaDataFromUrl() {
		var group = focused();
		if (group) {
			if (group.webresource) {
				return Promise.resolve(group.webresource.metainfo);
			}
		}
		return fetchDocumentInfo().then(docMetaInfo => {
			var uri = Promise.resolve(null);
			uri = getDocumentUriForGroupSearch();
			return uri.then(uri => { 
				if (uri) {
					var params = {};
					params.document_uri = uri;
					return api.group.getMetaData(params).then(metadata => {
						if(!_.isEmpty(metadata)) {
							if(docMetaInfo && docMetaInfo.metadata) {
								docMetaInfo.metadata.metatags = metadata;
							}
							return Promise.resolve(docMetaInfo);
						} else {
							return Promise.resolve(docMetaInfo);
						}
					}).catch(function () {
				      	return Promise.resolve(docMetaInfo);
				    });
				} else {
					return Promise.resolve(docMetaInfo);
				}
			});
		});
	}

	// Persist the focused group to storage when it changes.
	var prevFocusedId = store.focusedGroupId();
	store.subscribe(() => {
		var focusedId = store.focusedGroupId();
		var group = focused();
		var profile = store.profile();
		if (focusedId !== prevFocusedId) {
			prevFocusedId = focusedId;
			if(profile && profile.userid && !profile.isPublic) {
				if(userUIState && userUIState.stateValue) {
					if(userUIState.stateValue != focusedId) {
						userUIState.groupId = focusedId;
						saveGroupState(userUIState);
					}
				} else {
					saveGroupState({groupId:focusedId});
				}
			}
			localStorage.setItem(STORAGE_KEY, focusedId);

			// Emit the `GROUP_FOCUSED` event for code that still relies on it.
			$rootScope.$broadcast(events.GROUP_FOCUSED, focusedId);
		}
		var preferences;
		if (profile && profile.preferences) {
			preferences = store.profile().preferences;
		}
		frameSync.setGroup(group, preferences);
	});

	// reset the focused group if the user leaves it
	$rootScope.$on(events.GROUPS_CHANGED, function() {
		// return for use in test
		return load();
	});

	// refetch the list of groups when user changes
	$rootScope.$on(events.USER_CHANGED, (event, msg) => {
		// FIXME Makes a second api call on page load. better way?
		// return for use in test
		if (msg && msg.profile && msg.profile.userid) {
			return load();
		}
		store.loadGroups([]);
	});

	// refetch the list of groups when document url changes
	$rootScope.$on(events.FRAME_CONNECTED, (event, frameInfo) => {
		// FIXME Makes a third api call on page load. better way?
		// return for use in test
		docMetaInfo = null;
		return getDocumentUriForGroupSearch().then(uri => {
			if (documentUri !== uri) {
				documentUri = uri;
				//docMetaInfo = frameInfo;
				load();
			}
		});
	});


	function getTsObject(group, objId, objClientId) {
		var docObj = null;
		if (group && objId && objClientId && group.objects) {
			for (var i = 0; i < group.objects.length; i++) {
				let obj = group.objects[i];
				if (obj.objectId == objId && obj.clientId == objClientId) {
					docObj = obj;
					break;
				}
			}
		}

		return docObj;
	}



	function checkDocuLinkExists(group, tsObj) {
		var deferred = $q.defer();
		var postdata = {
			"objectType": "DigestDocument",
			"group": group.id,
			"tsClientId": group.clientId,
			"documentId": group.objId,
			"digestDocAnnotId": (tsObj && tsObj.digestDocAnnotId) ? tsObj.digestDocAnnotId : null
		};

		api.link.checkLinkExists({}, postdata).then(function(resp) {
			if (resp.status == "success") {
				if (!_.isEmpty(resp.link)) {
					deferred.resolve(resp.link);
				}
			}
			deferred.resolve(null);
		}, function() {
			deferred.resolve(null);
		});

		return deferred.promise;
	}

	function removeWebResource(group, actionOnAnnotations) {
		var deferred = $q.defer();
		var postdata = {
			"id": group.id,
			"clientId": group.clientId,
			"objects": [{
				"type": "Document",
				"objectId": group.objId,
				"clientId": group.objClientId
			}],
			"actionOnAnnotations": actionOnAnnotations
		};

		api.group.removeWebresource({}, postdata).then(function(resp) {
			if (resp.status == "success") {
				deferred.resolve(true);
			}
			deferred.resolve(false);
		}, function() {
			deferred.resolve(false);
		});

		return deferred.promise;
	}

	function deleteWebresource(group) {
		var deferred = $q.defer();
		var postdata = {};
		postdata["objectList"] = [{
			"objectType": "Document",
			"objectId": group.objId,
			"clientId": group.objClientId,
		}];
		api.group.deleteWebresource({}, postdata).then(function(resp) {
			if (resp.status == "success") {
				deferred.resolve(true);
			}
			deferred.resolve(false);
		}, function() {
			deferred.resolve(false);
		});

		return deferred.promise;
	}

	function getAnnotationCount(group) {
		var deferred = $q.defer();
		var postdata = {
			annotationContext: "taskspace",
			objId: group.objId,
			objClientId: group.objClientId,
			taskspaceId: group.id,
			tsClientId: group.clientId
		};
		api.annotation.annotationcount({}, postdata).then(function(resp) {
			if (resp.status == "success") {
				deferred.resolve(resp.count);
			}
			deferred.resolve(0);
		}, function() {
			deferred.resolve(0);
		});

		return deferred.promise;
	}
	
	
	function isReadOnly(group) {
		return group && group.permissions && group.permissions.readOnly;
	}
	
	function isReadAndComment(group) {
		return group && group.permissions && group.permissions.view && !(group.permissions.owner || group.permissions.edit);
	}
	
	function isEditable(group) {
		return group && group.permissions && (group.permissions.owner || group.permissions.edit);
	}
	
    function annotationCount(group) {
		return group && group.annotationCount || 0;
    }
 
    function hasWebResource(group) {
		return  group  &&  group.webresource ? true : false;
    }

    function canTag(group) {
		return isEditable(group) || ( isReadAndComment(group) && hasWebResource(group));
	}

    function isSelectable(group) {
	    if(isEditable(group)) {
			return true;
		}
		if(isReadAndComment(group)) {
			return hasWebResource(group);
		}
		if(isReadOnly(group)) {
			return annotationCount(group) > 0;
		}
	}
	
	function updateSection(section,documents) {
		if(!section) {
			section = {
				 "id": null,
			     "name": null,
			     "description": null,
			     "dateAdded": null,
	             "documents": [],
				 "collapsed": false,
			};
		} 
		if(documents) {
			section.documents = documents;
		}
		return Object.assign({collapsed: true},section);
	}
	
	function transformGroupSections(group) {
		var sections = [];
		if(group.sections) {
			for(var i = 0; i < group.sections.length; i++) {
				var documents = [],section;
				if(group.objects) {
					documents = group.objects.filter(function(document, index, arr) {
						return document.sectionId === group.sections[i].id;
					});
				}
				section = updateSection(group.sections[i],documents);
				sections.push(section);
			}
			
			if(group.objects) {
				var documents = group.objects.filter(function(document, index, arr) {
					return document.sectionId == null;
				});
				if(documents && documents.length > 0) {
					var section = updateSection(null,documents);
					sections.push(section);
				}
			}
			
			group.sections = sections; 
		} else {
			var section = updateSection(null,[].concat(group.objects || []));
			sections.push(section);
			group.sections = sections;
		}
		
		return group;
	}
	
	function createTaskspace(tsSearchTxt) {
		  if(createmodalInstance) {
			createmodalInstance.dismiss("cancel");
			createmodalInstance = null;
		  }

	      createmodalInstance = $modal.open({
				  animation: true,
				  template: require('../templates/create-taskspace.html'),
				  controller: 'CreateTaskspaceModalController',
				  controllerAs: 'vm',
				  backdrop: 'static',
				  size : 'lg',
		          resolve: {
					tsnamesuggestion: function() {
						return tsSearchTxt;
					}
				  }
		  });
		  return createmodalInstance.result;
	};
	
	function getDocSection(group) {
		let object, section;
		if(group && group.sections && group.webresource) {
			if(group.webresource.sectionId) {
				section = getSection(group,group.webresource.sectionId);
			    return section || null;
			}
			if(group.objects) {
				object = group.objects.find(function(obj) {
					return obj.objectId == group.webresource.objectId;
				});
				if (object) {
					section = getSection(group,object.sectionId);
				}
			    return section || null;
			}
		}
		return null;
	}
	
	function getSection(group,sectionId) {
		let section;
		if (group && group.sections) {
			section = group.sections.find(function(sect) {
				return sectionId == sect.id;
			});
			return section || null;
		}
		return null;
	}
	
	 function savefocusSection(group,section) {
		let postdata = Object.assign({},{
			id: group.id,
			sectionId: section ? section.id : null,
			clientId: group.clientId
		});
		return api.group.focusSection({},postdata).then(function(resp) {
			
		});
    }

    function getTaskspace(tsId,tsClientId) {
		var uri = Promise.resolve(null);
		var params = {
				expand: 'organization',
		};
		if (isSidebar) {
			uri = getDocumentUriForGroupSearch();
		}
		return uri.then(uri => {
			if (tsId) {
				params.tsId = tsId;
			}
			if (uri) {
				params.document_uri = uri;
			}
			if (tsClientId) {
				params.tsClientId = tsClientId;
			}
			return api.group.getTaskspace(params);
		}).then(gs => {
			return gs;
		});
    }
	
	return {
		all: all,
		get: get,
		openGroupdropDown: openGroupdropDown,

		leave: leave,
		load: load,
		updateCount: updateCount,
		updateGroup: updateGroup,

		focused: focused,
		getfocusedSection: getfocusedSection,
		focusedId: focusedId,
		getwebResource: getwebResource,
		getPdfbyUrl: getPdfbyUrl,
		focus: focus,
		focusSection: focusSection,
		addSection: addSection,
		getDocMetaInfo: getDocMetaInfo,
		fetchDocumentInfo: fetchDocumentInfo,
		getMetaDataFromUrl: getMetaDataFromUrl,
		getTsObject: getTsObject,
		checkDocuLinkExists: checkDocuLinkExists,
		removeWebResource: removeWebResource,
		deleteWebresource: deleteWebresource,
		getAnnotationCount: getAnnotationCount,
		
		isReadOnly: isReadOnly,
		annotationCount : annotationCount,
		isSelectable : isSelectable,
		canTag : canTag,
		isEditable: isEditable,
		transformGroupSections: transformGroupSections,
		
		createTaskspace: createTaskspace,
		getDocSection: getDocSection,
		getSection: getSection,
		savefocusSection: savefocusSection,
		getTaskspace: getTaskspace,
		loadAllGroups: loadAllGroups,
	};
}

module.exports = groups;
