'use strict';

/**
 * A service for displaying "confirm"  messages.
 */

// @ngInject
function MoveWebResource($modal, groups, flash, api) {
	var self = this;
	this.modalInstance = null;

	this.moveDocument = function(fromGroup, auth) {
		var modalOptions = {
			fromGroup: fromGroup,
			auth: auth
		};

		let modal = self.move(modalOptions);
		modal.then(function(postdata) {
			let move = null;
			if (postdata.fromTaskspaceId == postdata.toTaskspaceId) {
				move = moveToTaskspaceSection(postdata);
			}

			if (postdata.fromTaskspaceId != postdata.toTaskspaceId) {
				move = moveToTaskspace(postdata);
			}

			if (move) {
				move.then(function(resp) {
					groups.load(postdata.toTaskspaceId || postdata.taskspaceId);
				}).catch(function(err) {
					flash.error(err.message, 'Move web resource document failed', { extendedTimeOut: 0, tapToDismiss: false, timeOut: 0, closeButton: true, });
				});
			}
		}, function() { });
	};


	function moveToTaskspace(postdata) {
		return api.group.moveObject({}, postdata);
	}

    function updateforMove2section(postdata) {
		return Object.assign({}, {
			documentId: postdata.webresourceId,
			taskspaceId: postdata.fromTaskspaceId,
			clientId: postdata.fromClientId,
			sectionId: postdata.toSectionId,
		});
	}

	function moveToTaskspaceSection(postdata) {
		postdata = updateforMove2section(postdata);
		return api.group.move2section({}, postdata);
	}

	this.move = function(data) {
		if (this.modalInstance) {
			this.modalInstance.dismiss("cancel");
			this.modalInstance = null;
		}

		this.modalInstance = $modal.open({
			animation: true,
			template: require('../templates/move-to-taskspace.html'),
			controller: 'MoveToTaskspaceModal',
			controllerAs: 'vm',
			backdrop: 'static',
			size: 'lg',
			resolve: {
				moveData: function() {
					return data;
				}
			}
		});
		return this.modalInstance.result;
	};

}

module.exports = MoveWebResource;