'use strict';

var queryString = require('query-string');
/**
 * A service for displaying "confirm"  messages.
 */

// @ngInject
function PostToSocialMediaModal($scope,api,$modalInstance,DigestLinkinfo,
		shareCommentaryTxt,authUsrPreferences,MediaChannelInfo,_,flash,confirm,$window,$timeout) {
	var self = this;
	
	// Instance variables
	var longHyphenTxt = "\u2014";
	var newlineTxt = "\n";
	var doubleNewlineTxt = "\n\n";
	var digestSettings = {};
	if(!_.isEmpty(DigestLinkinfo.properties)) {
		digestSettings = angular.copy(DigestLinkinfo.properties.digestSettings);
	} else if(!_.isEmpty(DigestLinkinfo.linkObj.properties)) {
		digestSettings = angular.copy(DigestLinkinfo.linkObj.properties.digestSettings);
	}
	
	self.mediaChannel = MediaChannelInfo.channel;
	self.DigestInfo = {"title" : digestSettings.digestName,"shareCommentary" : !_.isEmpty(shareCommentaryTxt.text) ? shareCommentaryTxt.text : digestSettings.digestDescription};
	self.authUsrPreferences = angular.copy(authUsrPreferences);
	self.articleId;
	self.focusLink = false;
	self.visibilityOptionList = [
     		                        {"label" : "CONNECTIONS","value" : "CONNECTIONS"},
    		                        {"label" : "GROUP OR PRIVATE MESSAGE","value" : "CONTAINER"},
    		                        {"label" : "PUBLIC","value" : "PUBLIC"},
    		                    ];
	self.visibilityOption = self.visibilityOptionList[0];
	
	self.shareCommentaryMaxText = 230;
	self.shareCommentaryTextRemaining = angular.copy(self.shareCommentaryMaxText);
	
	self.NumiciMktMsgTxt = "Curated using Numici.com Sign up today!";
	self.NumiciMktMsg = longHyphenTxt+"<br>"+self.NumiciMktMsgTxt;
	
	// Methods
	self.checkShareCommentaryCharLimit = checkShareCommentaryCharLimit;
	self.postSocialMediaArticle = postSocialMediaArticle;
	self.cancel = cancel;
	
	$scope.$on("LinkedInAuthNotification", function (event, message) {
		if(message.HasLinkedInAuth && message.HasLinkedInAuth == "true") {
			authUsrPreferences.hasLinkedInAuth = true;
		} else if(message.HasLinkedInAuth && message.HasLinkedInAuth == "false") {
			authUsrPreferences.hasLinkedInAuth = false;
		}
		if(message.LinkedInAuthUrl) {
			authUsrPreferences.LinkedInAuthUrl = message.LinkedInAuthUrl;
		}
	});
	
	function checkShareCommentaryCharLimit(event) {
		var CharLenLimitTimeout = $timeout(function() {
			if(self.DigestInfo.shareCommentary != null && self.DigestInfo.shareCommentary != undefined) {
				self.shareCommentaryTextRemaining = self.shareCommentaryMaxText - self.DigestInfo.shareCommentary.length;
			}
			$timeout.cancel(CharLenLimitTimeout);
		}, 100);
	}
	
	function getAuthEnabledUsrConfMsg(mediaChannel) {
		var text = "<p>Numici cannot post to groups or private messages directly.</p>\
			<ul style='list-style: disc;padding-left: 40px;'>\
			<li>Click on 'Go to "+mediaChannel+"'</li>\
			<ul style='list-style: circle;padding-left: 40px;'>\
				<li>Numici will copy share content to the clipboard</li>\
				<li>Numici will take you to share content page on "+mediaChannel+"</li>\
				<li>Select Share in a Post or Send as Private Message</li>\
				<li>Copy content from clipboard</li>\
				<li>Select how you want to share the post in the dropdown</li>\
				<li>Click on Post</li>\
			</ul>\
		</ul>";
		if(mediaChannel == "Twitter" && !_.isEmpty(self.authUsrPreferences.TwitterAuthEnabledForUsrConfMsgText)) {
			text = self.authUsrPreferences.TwitterAuthEnabledForUsrConfMsgText;
		}
		if(mediaChannel == "Facebook" && !_.isEmpty(self.authUsrPreferences.FacebookAuthEnabledForUsrConfMsgText)) {
			text = self.authUsrPreferences.FacebookAuthEnabledForUsrConfMsgText;
		}
		if(mediaChannel == "LinkedIn" && !_.isEmpty(self.authUsrPreferences.linkedInAuthEnabledForUsrConfMsgText)) {
			text = self.authUsrPreferences.linkedInAuthEnabledForUsrConfMsgText;
		}
		if(mediaChannel == "Whatsapp" && !_.isEmpty(self.authUsrPreferences.WhatsappAuthEnabledForUsrConfMsgText)) {
			text = self.authUsrPreferences.WhatsappAuthEnabledForUsrConfMsgText;
		}
		return text;
	}
	
	function getAuthNotEnabledUsrConfMsg(mediaChannel) {
		var text = "<p>Click on 'Go to "+mediaChannel+"'</p>\
			<ul style='list-style: disc;padding-left: 40px;'>\
				<li>Numici will copy share content to the clipboard</li>\
				<li>Numici will take you to share content page on "+mediaChannel+"</li>\
				<li>Select Share in a Post or Send as Private Message</li>\
				<li>Copy content from clipboard</li>\
				<li>Select how you want to share the post in the dropdown</li>\
				<li>Click on Post</li>\
			</ul>";
		
		if(mediaChannel == "Facebook") {
			text = "<p>Click on 'Go to Facebook'</p>\
					<ul style='list-style: disc;padding-left: 40px;'>\
						<li>Numici will copy share content to the clipboard</li>\
						<li>Numici will take you to 'Share on Facebook' page</li>\
						<li>Paste content from clipboard in text area</li>\
						<li>Select how you want to share the post in the dropdown</li>\
						<li>Click on 'Post on Facebook'</li>\
					</ul>"
		}
		if(mediaChannel == "Twitter" && !_.isEmpty(self.authUsrPreferences.TwitterAuthNotEnabledForUsrConfMsgText)) {
			text = self.authUsrPreferences.TwitterAuthNotEnabledForUsrConfMsgText;
		}
		if(mediaChannel == "Facebook" && !_.isEmpty(self.authUsrPreferences.FacebookAuthNotEnabledForUsrConfMsgText)) {
			text = self.authUsrPreferences.FacebookAuthNotEnabledForUsrConfMsgText;
		}
		if(mediaChannel == "LinkedIn" && !_.isEmpty(self.authUsrPreferences.linkedInAuthNotEnabledForUsrConfMsgText)) {
			text = self.authUsrPreferences.linkedInAuthNotEnabledForUsrConfMsgText;
		}
		if(mediaChannel == "Facebook" && !_.isEmpty(self.authUsrPreferences.WhatsappAuthNotEnabledForUsrConfMsgText)) {
			text = self.authUsrPreferences.WhatsappAuthNotEnabledForUsrConfMsgText;
		}
		return text;
	}
	
	function postArticleOnSocialMediaGroup(showAPIPostFields) {
		var confirmationMsgText = "";
		if(showAPIPostFields) {
			confirmationMsgText = getAuthEnabledUsrConfMsg(self.mediaChannel)
		} else {
			confirmationMsgText = getAuthNotEnabledUsrConfMsg(self.mediaChannel)
		}
		
		var modalOptions = {
          closeButtonText: 'CANCEL',
          actionButtonText: 'Go To '+self.mediaChannel,
          headerText: 'Post message on '+self.mediaChannel,
          bodyText: confirmationMsgText
		};
		return confirm.showModal({}, modalOptions);
	}
	
	function shareArticleOnLinkedin(postdata) {
		api.link.shareArticleOnLinkedin({},postdata).then(function(resp) {
			if(resp.Status) {
				flash.success("Article shared on LinkedIn successfully"); 
				self.article = resp.article;
				self.message = resp.message;
			} else if(!resp.Status && !_.isEmpty(resp.Message)) {
				flash.error(resp.Message,"Share Article On LinkedIn",{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
			}
		});
	}
	
	function postArticleOnSocialMedia(mediaChannel) {
		var postdata = {};
		postdata.title = self.DigestInfo.title;
		postdata.shareCommentary = self.DigestInfo.shareCommentary + doubleNewlineTxt + longHyphenTxt + newlineTxt + self.NumiciMktMsgTxt;
		postdata.shareUrl = DigestLinkinfo.url;
		postdata.visibilityOption = self.visibilityOption ? self.visibilityOption.value : null;
		if(mediaChannel == "LinkedIn") {
			shareArticleOnLinkedin(postdata);
		}
	}
	
	function openJSPopupWindow(mediaChannel,locationUrl) {
	    // In Chrome & Firefox the sizes passed to `window.open` are used for the
	    // viewport size. In Safari the size is used for the window size including
	    // title bar etc. There is enough vertical space at the bottom to allow for
		var width  = 550;
	    var height = 475;
	    var left   = $window.screen.width / 2 - width / 2;
	    var top    = $window.screen.height /2 - height / 2;
	    
	    // Generate settings for `window.open` in the required comma-separated
	    var JSPopupWindowSettings = queryString.stringify({
	      left: left,
	      top: top,
	      width: width,
	      height: height,
	    }).replace(/&/g, ',');
	    
	    var newWindow = $window.open('about:blank', "Share On "+mediaChannel, JSPopupWindowSettings);
	    newWindow.location = locationUrl;
	}
	
	function postSocialMediaArticle() {
		var postText = self.DigestInfo.shareCommentary + doubleNewlineTxt + longHyphenTxt + newlineTxt + self.NumiciMktMsgTxt;
        if(self.mediaChannel == "Twitter") {
			var url = "https://twitter.com/intent/tweet?url="+encodeURIComponent(DigestLinkinfo.url+"&hashtags=annotated")+"&text="+encodeURIComponent(postText);
			openJSPopupWindow(self.mediaChannel,url);
		}
        if(self.mediaChannel == "Whatsapp") {
			var url = "https://api.whatsapp.com/send?text="+encodeURIComponent(postText+doubleNewlineTxt+DigestLinkinfo.url);
			openJSPopupWindow(self.mediaChannel,url);
		}
        if(self.mediaChannel == "LinkedIn" || self.mediaChannel == "Facebook") {
        	if((self.visibilityOption && self.visibilityOption.value == "CONTAINER") || !self.authUsrPreferences.showAPIPostFields) {
    			postArticleOnSocialMediaGroup(self.authUsrPreferences.showAPIPostFields).then(function() {
    				var body = angular.element($window.document.body);
                    var textarea = angular.element('<textarea/>');
                    textarea.css({
                        position: 'fixed',
                        opacity: '0'
                    });
                    textarea.val(postText);
                    body.append(textarea);
                    textarea[0].select();
                    $window.document.execCommand('copy');
                    textarea.remove();
                    if(self.mediaChannel == "Facebook") {
            			var url = "https://www.facebook.com/sharer/sharer.php?u="+encodeURIComponent(DigestLinkinfo.url);
            			openJSPopupWindow(self.mediaChannel,url);
            		}
                    if(self.mediaChannel == "LinkedIn") {
                    	var url = "https://www.linkedin.com/sharing/share-offsite/?url="+encodeURIComponent(DigestLinkinfo.url);
                    	openJSPopupWindow(self.mediaChannel,url);
            		}
                    $modalInstance.close();
                });
    		} else {
    			postArticleOnSocialMedia(self.mediaChannel);
    		}
        } else {
			$modalInstance.close();
		}
	}
	
	function cancel() {
		$modalInstance.dismiss("cancel");
	}
	
	function init() {
		var NumiciMktMsgLocal = "Curated using Numici.com Sign up today!";
		if(self.mediaChannel == "Twitter") {
			self.shareCommentaryMaxText = !_.isEmpty(self.authUsrPreferences.TwitterShareCommentaryMaxTextLen) ? self.authUsrPreferences.TwitterShareCommentaryMaxTextLen : 196;
			self.NumiciMktMsgTxt = !_.isEmpty(self.authUsrPreferences.NumiciMktMsgTxtForTwitter) ? self.authUsrPreferences.NumiciMktMsgTxtForTwitter : NumiciMktMsgLocal;
		}
		if(self.mediaChannel == "Facebook") {
			self.shareCommentaryMaxText = !_.isEmpty(self.authUsrPreferences.FacebookShareCommentaryMaxTextLen) ? self.authUsrPreferences.FacebookShareCommentaryMaxTextLen : 63,156;
			self.NumiciMktMsgTxt = !_.isEmpty(self.authUsrPreferences.NumiciMktMsgTxtForFacebook) ? self.authUsrPreferences.NumiciMktMsgTxtForFacebook : NumiciMktMsgLocal;
		}
		if(self.mediaChannel == "LinkedIn") {
			self.shareCommentaryMaxText = !_.isEmpty(self.authUsrPreferences.LinkedInShareCommentaryMaxTextLen) ? self.authUsrPreferences.LinkedInShareCommentaryMaxTextLen : 1250;
			self.NumiciMktMsgTxt = !_.isEmpty(self.authUsrPreferences.NumiciMktMsgTxtForLinkedIn) ? self.authUsrPreferences.NumiciMktMsgTxtForLinkedIn : NumiciMktMsgLocal;
		}
		if(self.mediaChannel == "Whatsapp") {
			self.shareCommentaryMaxText = !_.isEmpty(self.authUsrPreferences.WhatsappShareCommentaryMaxTextLen) ? self.authUsrPreferences.WhatsappShareCommentaryMaxTextLen : 64000;
			self.NumiciMktMsgTxt = !_.isEmpty(self.authUsrPreferences.NumiciMktMsgTxtForWhatsApp) ? self.authUsrPreferences.NumiciMktMsgTxtForWhatsApp : NumiciMktMsgLocal;
		}
		self.shareCommentaryTextRemaining = angular.copy(self.shareCommentaryMaxText);
		self.NumiciMktMsg = longHyphenTxt+"<br>"+self.NumiciMktMsgTxt;
		checkShareCommentaryCharLimit();
	}
	init();
}
module.exports = PostToSocialMediaModal;