'use strict';
var events = require('../events');

/* @ngInject */
function MoveToTaskspaceModalController($scope, groups, $modalInstance, moveData, moveDocCorfirm,$q,userPreference) {
	'ngInject';
	var self = this;
	self.groups = groups;
	self.from = "";
	self.modalOptions = {
		cancel: 'Cancel',
		moveTsButtonText: 'Move',
		moveSecButtonText: 'Move To Taskspace Section',
		headerText: 'Move Web Resource',
		addSection: 'Add Section',
	};


	self.moveConfimation = function(fromGroup,postdata) {
		let link = groups.checkDocuLinkExists(fromGroup).then(function(link) {
			return link;
		});

		/*let annotCount = groups.getAnnotationCount(fromGroup).then(function(count) {
			return count;
		});*/

		$q.all([link]).then(function(resp) {
			if (resp && resp.length > 0) {
				if (resp[0]) {
					let data = {
						link: resp[0],
					};
					moveDocCorfirm.confirm(data).then(function(moveOrdeleteAnnot) {
						postdata.actionOnAnnotations = moveOrdeleteAnnot;
						$modalInstance.close(postdata);
					});
				} else {
					$modalInstance.close(postdata);
				}
			}
		});
	};
	
	self.getDocFrom = function() {
		if (self.from) {
			return self.from;
		}
		if (self.fromGroup) {
			self.from = self.fromGroup.name;
			if (self.fromGroup.webresource) {
				let section = groups.getDocSection(self.fromGroup);
				if (section) {
					self.from = self.from + " / " + section.name;
				}
			}
		}
		return self.from;
	}

	$scope.$watch(angular.bind(self, function() {
		return self.group;
	}), function(newGroup, oldGroup) {
		if(newGroup && oldGroup && newGroup.id != oldGroup.id) {
			self.group = newGroup;
			self.section = self.group ? self.groups.getfocusedSection(self.group.id) : {};
		}
	});


	$scope.$on(events.GROUP_UPDATED, function(event, groupId) {
		if (self.group && self.group.id == groupId) {
			self.group = groups.get(groupId);
		}
	});

	self.cancel = function() {
		$modalInstance.dismiss('cancel');
	};

	self.movetToTaskspace = function() {
		let postdata = update();
		if(postdata.fromTaskspaceId == postdata.toTaskspaceId) { 
			$modalInstance.close(postdata);
		}
		
		if(postdata.fromTaskspaceId != postdata.toTaskspaceId && self.fromGroup) { 
			self.moveConfimation(self.fromGroup,postdata);
		}
		
	};

	self.movetToSection = function() {
		let data = update();
		$modalInstance.close(data);
	};
    
	self.allowCreateSection = userPreference.canCreateSection;
	self.hasSections = function() {
		if (self.group) {
			return self.group.sections && self.group.sections.length > 0;
		}
		return false;
	};

	function update() {
		return Object.assign({}, {
			webresourceId: self.fromGroup && self.fromGroup.webresource ? self.fromGroup.webresource.objectId : null,
			webresourceClientId: self.fromGroup && self.fromGroup.webresource ? self.fromGroup.webresource.clientId : null,
			
			fromTaskspaceId: self.fromGroup ? self.fromGroup.id : null,
			fromClientId: self.fromGroup  ? self.fromGroup.clientId : null,
			
			toTaskspaceId: self.group ? self.group.id : null,
			toClientId: self.group ? self.group.clientId : null,
			
			toSectionId: self.section ? self.section.id : null,
		});
	}

	self.onNewSection = function(data) {
		self.section = data.section;
		//groups.addSection(self.group.id,data.section);
	}

	self.disableMove = function() {
		if (self.fromGroup.id != self.group.id) {
			return false;
		}

		if (self.fromGroup.id == self.group.id) {
			let sectToTs = (self.fromSection && !self.section) ? true : false;
			let tsToSec = (!self.fromSection && self.section) ? true : false;
			let secTosec = (self.fromSection && self.section && self.fromSection.id != self.section.id) ? true : false;

			return !(sectToTs || tsToSec || secTosec);
		}
		return true;
	};

	function init() {
		self.group = angular.copy(moveData.fromGroup);
		self.fromGroup = moveData.fromGroup;
		if (self.fromGroup) {
			self.fromSection = groups.getDocSection(self.fromGroup);
			self.section = angular.copy(self.fromSection);
		} else {
			self.fromSection = null;
		}
		self.auth = moveData.auth;
	}

	init();
}

module.exports = MoveToTaskspaceModalController;
