'use strict';

// @ngInject
function TagEditorController(tags,api) {
	
  this.onTagAdding = function($tag) {
	  var status = true;
	  var txt = $tag.text;
	  if(txt.indexOf(":") > -1) {
		  txt = txt.split(":")[0];
	  }
	  
	  if(!txt) {
		status = false;
		return status;
	  }
	  
	  this.tagList.forEach(function (item) {
		  var itemText = item.text;
		  if(itemText.indexOf(":") > -1) {
			  itemText = itemText.split(":")[0];
			  if(!itemText) {
				status = false;
				return status;
			  }
		  }
		  
		  if(itemText && txt && itemText.trim().toLowerCase() === txt.trim().toLowerCase()) {
				status = false;
				return status;
		  }
	  });
	  
	  return status;
  };
  
  this.onTagsChanged = function () {
    tags.store(this.tagList);

    var newTags = this.tagList.map(function (item) {
    	if(item.text.indexOf(":") > -1) {
			var tagInfo = item.text.split(":");
			if(tagInfo[1]) {
				return {"TagName": tagInfo[0].trim(),"Value": tagInfo[1].trim()};
			}
		}
    	return {"TagName": item.text};
    });
    this.onEditTags({tags: newTags});
  };

  this.autocomplete = function (query) {
	return api.tags.searchResourceTags({}, { "searchKey": query }).then(function(resp) {
		if (resp && resp.length > 0) {
			return resp.map(function(tag) {
				if (tag.Value) {
					return { text: tag.TagName + ":" + tag.Value ,tagId: tag.TagId , isNew: tag.isNew};
				}
				return { text: tag.TagName, tagId: tag.TagId , isNew: tag.isNew};
			});
		} else {
			return [{ text: query, tagId: null , isNew: true}];
		}
	});
  };

  this.$onChanges = function (changes) {
    if (changes.tags) {
      this.tagList = changes.tags.currentValue.map(function (tag) {
    	  if(tag.Value) {
    		  return {text: tag.TagName+":"+tag.Value};
    	  }
        return {text: tag.TagName};
      });
    }
  };
}

module.exports = {
  controller: TagEditorController,
  controllerAs: 'vm',
  bindings: {
    tags: '<',
    onEditTags: '&',
  },
  template: require('../templates/tag-editor.html'),
};
