'use strict';

var events = require('../events');


/**
 * @typedef Profile
 *
 * An object returned by the API (`GET /api/profile`) containing profile data
 * for the current user.
 */

/**
 * This service handles fetching the user's profile, updating profile settings
 * and logging out.
 *
 * Access to the current profile is exposed via the `state` property.
 *
 * @ngInject
 */
function userPreference($rootScope, store, settings) {
 
  function canCreateTaskspace() {
	var profile = store.profile();
	var pref = profile && profile.preferences ? profile.preferences : null;
	if(pref) {
		return profile.userid && !profile.isPublic && pref.createTaskspace;
	}
	return false;
  }
  
  function canCreateSection() {
	var profile = store.profile();
	var pref = profile && profile.preferences ? profile.preferences : null;
	if(pref) {
		return profile.userid && !profile.isPublic && pref.createSection;
	}
	return false;
  }
  

  return {
    canCreateTaskspace,
    canCreateSection,
  };
}

module.exports = userPreference;
