'use strict';

/**
 * A service for displaying "confirm"  messages.
 */

// @ngInject
function ConfirmLinkPublish($modalInstance) {
	var self = this;
	
	// Methods
	self.confirmAction = confirmAction;
	self.cancel = cancel;
			
	function confirmAction(action) {
		$modalInstance.close({"action" : action});
	}
	
	function cancel() {
		$modalInstance.dismiss("cancel");
	}
}

module.exports = ConfirmLinkPublish;