'use strict';
var events = require('../events');

// @ngInject
function globalErrorHandler($q, $rootScope, timelog) {


	function setRespomnseTimeStamp(response) {
		if (response.config.url && response.config.url.indexOf("api/") >= 0 || response.config.url.indexOf("annotateWebApi/") >= 0) {
			response.config.responseTimestamp = new Date().getTime();
			var time = response.config.responseTimestamp - response.config.requestTimestamp;
			time = (time / 1000);
			if (time >= 0) {
				var timeLog = {
					'timeStamp': response.config.requestTimestamp,
					'resp_timeStamp': response.config.responseTimestamp,
					'requestedUrl': response.config.url,
					'httpStatus': "NA",
					'responseTime': time,
					'serverExeTime' : response.headers()["response-time"]
				};
				timelog.setTimeLogInfo(timeLog);
			}
		}
	}



	function responseErrorHandler(response) {
		if (response.status === 401) {
			$rootScope.$broadcast(events.OAUTH_TOKENS_ERROR);
		}
		return $q.reject(response);
	}


	function responseHandler(response) {
		if (response.data && response.data.status === "failure") {
			return $q.reject(response);
		}
		setRespomnseTimeStamp(response);
		return response;
	}


	function request(config) {
		if (config.url && (config.url.indexOf("api/") >= 0 || config.url.indexOf("annotateWebApi/") >= 0)) {
			config.requestTimestamp = new Date().getTime();
		}
		return config;
	}


	return {
		responseError: responseErrorHandler,
		response: responseHandler,
		request: request
	};
}

module.exports = globalErrorHandler;
