'use strict';
var events = require('../events');

/* @ngInject */
function DocRemoveConfirmModal($scope, $modalInstance,confirmdata,modalOptions,taskspace) {
	'ngInject';
	var self = this;
	
	self.modalOptions = modalOptions;
	self.deleteAnnotations = {option: null};
	self.deleteObject = false;
	self.remove = remove;
	self.cancel = cancel;

	function remove() {
		$modalInstance.close({ "deleteObject": self.deleteObject , "deleteAnnot" : self.deleteAnnotations.option ? self.deleteAnnotations.option : null});
	}

	function cancel() {
		$modalInstance.dismiss('cancel');
	}
	 
    self.disableConfirm = function() {
	    if(self.hasAnnotations) {
			return self.deleteAnnotations.option ? false : true;
	    }
        return false;
	};
	
	function init() {
		self.hasDocLink = confirmdata.link;
		self.hasAnnotations = confirmdata.count;
	}
	
	init();
}

module.exports = DocRemoveConfirmModal;
