'use strict';

/**
 * A service for displaying "confirm"  messages.
 */

// @ngInject
function TimeLogModal($scope,$modalInstance,timelog,$timeout) {
	var self = this;
	var vm = this; 
		
	vm.timeLog = timelog.getTimeLogInfo();
	vm.cancel = cancel;
	vm.clearlog = clearlog;
	vm.tableToExcel = tableToExcel;
	
	function clearlog() {
		timelog.clearLog();
		vm.timeLog = timelog.getTimeLogInfo();
	}
	
	function cancel() {
		$modalInstance.dismiss('cancel');
	}
	
	function tableToExcel() {
		var exportHref = timelog.tableToExcel("#timeLog","timeLog");
		$timeout(function(){
			
			var dt = new Date();
	        var day = dt.getDate();
	        var month = dt.getMonth() + 1;
	        var year = dt.getFullYear();
	        var hour = dt.getHours();
	        var mins = dt.getMinutes();
	        var postfix = day + "." + month + "." + year + "_" + hour + "." + mins;
	        //creating a temporary HTML link element (they support setting file names)
	        var a = document.createElement('a');
	        //getting data from our div that contains the HTML table
	        a.href = exportHref;
	        //setting the file name
	        a.download = 'time_log'+ postfix + '.xls';
	        //triggering the function
	        a.click();
		},100);
	}
}

module.exports = TimeLogModal;