'use strict';

var scrollIntoView = require('scroll-into-view');

var events = require('../events');
var { parseAccountID } = require('../util/account-id');
var scopeTimeout = require('../util/scope-timeout');
var serviceConfig = require('../service-config');
var bridgeEvents = require('../../shared/bridge-events');


/**
 * Return the user's authentication status from their profile.
 *
 * @param {Profile} profile - The profile object from the API.
 */
function authStateFromProfile(profile) {
	if (profile.userid) {
		var parsed = parseAccountID(profile.userid);
		var displayName = (parsed && parsed.username) ? parsed.username : "";
		if (profile.user_info && profile.user_info.display_name) {
			displayName = profile.user_info.display_name;
		}
		return {
			status: 'logged-in',
			displayName,
			userid: profile.userid,
			username: (parsed && parsed.username) ? parsed.username : "",
			provider: (parsed && parsed.provider) ? parsed.provider : "",
			isPublic: profile.isPublic,
			signup: profile.signup,
			thirdPartyCookies: profile["third-party-cookie"],
			preferences: profile.preferences,
			authority: profile.authority,
			inMaintenanceMode: profile.inMaintenanceMode,
			ActiveSchedule: profile.ActiveSchedule,
			UserRole: profile.UserRole
		};
	} else {
		return {
			status: 'logged-out',
			thirdPartyCookies: profile["third-party-cookie"],
			signup: profile.signup,
			isPublic: profile.isPublic,
			inMaintenanceMode: profile.inMaintenanceMode,
			ActiveSchedule: profile.ActiveSchedule,
			UserRole: profile.UserRole
		};
	}
}


// @ngInject
function HypothesisAppController(
	$document, $location, $rootScope, $route, $scope,
	$window, analytics, store, auth, bridge, drafts, features,
	flash, frameSync, groups, serviceUrl, session, settings, streamer,
	moveWebResource, api, localStorage, $modal, sharelinks, _, confirm, $q, maintenance,addtotaskspace
) {
	var self = this;
	var sharelinkWindow;
	// This stores information about the current user's authentication status.
	// When the controller instantiates we do not yet know if the user is
	// logged-in or not, so it has an initial status of 'unknown'. This can be
	// used by templates to show an intermediate or loading state.
	this.auth = { status: 'unknown' };
	this.maintanencedata = {
		visible: false
	};
	// App dialogs
	this.shareDialog = { visible: false };
	this.cookieDialog = { visible: false };
	this.maintenanceDialog = { visible: false };
	this.shutdownNtf = { visible: false };
	this.sheduleNtf = { visible: false };
	this.groupInfoDialog = { visible: false };
	this.tagInfoDialog = { visible: false };
	this.createGroupDialog = { visible: false };
	this.aboutPanel = { visible: false };
	this.helpPanel = { visible: false };

	// Check to see if we're in the sidebar, or on a standalone page such as
	// the stream page or an individual annotation page.
	this.isSidebar = $window.top !== $window;
	if (this.isSidebar) {
		frameSync.connect();
	}

	//add to taskspce loader
	this.addToTsLoader = false;

	this.showExtShare = function() {
		var shareLinks = false;
		if (store.profile().preferences) {
			shareLinks = store.profile().preferences["shareLinks"] === 'true' ? true : false;
		};
		return shareLinks
	};

	this.sortKey = function() {
		return store.getState().sortKey;
	};

	this.sortKeysAvailable = function() {
		return store.getState().sortKeysAvailable;
	};

	this.setSortKey = store.setSortKey;

	// Reload the view when the user switches accounts
	$scope.$on(events.USER_CHANGED, function(event, data) {
		data = data || {};
		self.auth = authStateFromProfile(data.profile || {});
		frameSync.setUser(self.auth);
		showActiveSchedule();
		closeShareWindow();
	});

    // Reload the view when the user switches accounts
	$scope.$on("urlChanged", function(event, data) {
		/*showActiveSchedule();
		closeShareWindow();
		groups.load();*/
		frameSync.urlChanged();
	});

    $scope.$watch(() => ([groups.focusedId(),store.profile().userid]), ([currentGroupId], [prevGroupId]) => {
	  	if(currentGroupId !== prevGroupId ) {
	  		 closeShareWindow();
	  	}
    }, true);

	/*$scope.$on("windowFocus", () => {
		setTimeout(function() {
			if (sharelinkWindow && sharelinkWindow != null && !sharelinkWindow.closed) {
				sharelinkWindow.focus();
			}
		}, 100);
	});

    $window.addEventListener('beforeunload', function() {
       closeShareWindow();
    });

    $scope.$on("$destroy", function () {
	   closeShareWindow();
	});*/
    
	$scope.$on("linkshare", function(event, data) {
		self.share(data.annotationId,null);
	});

	$scope.$on("shareDocument", function(event, data) {
		self.share(null,data);
	});

	$scope.$on("moveDocument", function(event, data) {
		moveDocToTaskspace();
	});
	
	$scope.$on("tagDocument", function(event, data) {
		self.tagDocument(data);
	});

	$scope.$on(events.ADD_TO_TASKSPACE, function(event, data) {
		if (store.focusedGroup()) {
			var group = groups.focused();
			data.tsId = groups.focusedId();
			data.tsClientId = group ? group.clientId : null;

			//data.objId = group ? group.objId : null;
			//data.objClientId = group ? group.objClientId : null;

			if (!data.objId) {
				addToTaskSpace(data);
			} else {
				flash.info("WebResource is already added to TaskSpace");
			}
		} else {
			if (!session.state.userid) {
				flash.error("Please Login to Save WebResource",{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
			} else {
				flash.error("Please Select a taskspace to Save WebResource",{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
			}
		}

	});

	$scope.$on("HTML_OBJ_ANNOTATIONS", function(event, data) {
		frameSync.enableOrDisableObjectAnnotations(data);
	});

	$scope.$on(events.REQUEST_LOGIN, function(event, data) {
		self.login();
	});


	/* $scope.$on("updatedAuth", function(event, message) {
		 if(message) {
			 self.auth.inMaintenanceMode = message.inMaintenanceMode;
			 self.auth.ActiveSchedule =message.ActiveSchedule;
			 self.auth.UserRole =message.UserRole;
		 }
	 });*/


	$scope.$on("SHEDULED_MAINTENANCE", function(event, msg) {
		if (!_.isEmpty(msg.scheduleId)) {
			maintenance.getScheduleById(msg.scheduleId).then(function(resp) {
				if (resp.Status) {
					self.maintanencedata = resp.Schedule;
					if (self.maintanencedata) {
						self.maintenanceDialog.visible = true;
						self.maintanencedata.notification = true;
					} else {
						self.maintanencedata = { visible: false };
					}
				}
			});
		}
	});

	$scope.$on("SHEDULED_SHUTDOWN", function(event, msg) {
		if (!_.isEmpty(msg.scheduleId)) {
			maintenance.getScheduleById(msg.scheduleId).then(function(resp) {
				if (resp.Status) {
					self.maintanencedata = resp.Schedule;
					if (self.maintanencedata) {
						self.maintenanceDialog.visible = true;
						self.maintanencedata.shutdown = true;
					} else {
						self.maintanencedata = { visible: false };
					}
				}
			});
		}
	});


	function addToTaskSpace(data) {
		return groups.getMetaDataFromUrl().then(docMetaInfo => { 
			data.document = docMetaInfo.metadata;
			data.uri = docMetaInfo.uri;
			if (docMetaInfo && docMetaInfo.metadata && (docMetaInfo.metadata.filename || docMetaInfo.metadata.pdfInfo)) {
				data.isPdf = true;
			}
			
			let defer = api.createPromise();
			let promise = defer.promise;
			var focusedgroup = groups.focused();
			if(data.isPdf ) {
				if(groups.getPdfbyUrl(focusedgroup,data.uri)) {
					defer.resolve(focusedgroup);
					return promise;
				}
			} else if(focusedgroup && focusedgroup.objId) {
				defer.resolve(focusedgroup);
				return promise;
			}
			frameSync.hideSidePaneToolbar(true,focusedgroup);
			let addTotsModal = addtotaskspace.add(self.auth);
			addTotsModal.then(function(result) {
				if(result && result.group) {
					var group = result.group;
				    data.tsId = group.id;
				    data.tsClientId = group.clientId;
	                data.sectionId = result.section ? result.section.id : null;
				    data.objId = group.objId;
					if( data.objId ) {
						defer.resolve(group);
						return promise;
					}
				}
				self.addToTsLoader = true;
				api.annotation.addToTaskSpace({}, data).then(function(resp) {
					if (resp.message) {
						flash.info(resp.message);
					} else {
						flash.success("WebResource added to TaskSpace successfully");
						store.updateGroup(data.tsId, 0, resp.webresource);
						groups.focus(data.tsId);
						if(data.sectionId) {
							groups.focusSection(data.tsId,result.section);
						}
					}
					defer.resolve(resp);
				}).catch(function(err) {
					flash.error(err.message, 'Saving Document failed',{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
					defer.resolve(null);
				}).finally(function() {
					self.addToTsLoader = false;
					frameSync.hideSidePaneToolbar(false,focusedgroup);
				});
				
			},function(){
				defer.resolve(null);
			});
			promise.finally(function() {
				frameSync.hideSidePaneToolbar(false,focusedgroup);
			});
			return promise;
		});
	}

	

	$scope.$on("LinkedInAuthNotification", function(event, message) {
		if (message.HasLinkedInAuth && message.HasLinkedInAuth == "true") {
			self.auth.preferences.hasLinkedInAuth = true;
		} else if (message.HasLinkedInAuth && message.HasLinkedInAuth == "false") {
			self.auth.preferences.hasLinkedInAuth = false;
		}
		if (message.LinkedInAuthUrl) {
			self.auth.preferences.LinkedInAuthUrl = message.LinkedInAuthUrl;
		}
	});


	function showActiveSchedule() {
		if (self.auth.inMaintenanceMode) {
			if (!_.isEmpty(self.auth.ActiveSchedule) && !_.isEmpty(self.auth.ActiveSchedule.Schedule)) {
				self.maintanencedata = self.auth.ActiveSchedule.Schedule;
				self.maintenanceDialog.visible = true;
				self.maintanencedata.shutdown = true;
			}
		} else {
			self.maintenanceDialog.visible = false;
		}
	}

	session.load().then(profile => {
		if (profile.reason && profile.reason != "") {
			flash.error(profile.reason,{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
		} else {
			self.auth = authStateFromProfile(profile);
			frameSync.setUser(self.auth);
			if (self.auth.status !== 'logged-in') {
				if (settings.appType === "embedded" || (!profile.isPublic && !profile.userid)) {
					self.login();
					$window.focus();
				}
			}
			var objAnnotations = localStorage.getItem("HTML_OBJ_ANNOTATIONS");
			frameSync.enableOrDisableObjectAnnotations(objAnnotations);
			showActiveSchedule();
			if (self.auth && self.auth.isPublic) {
				groups.load();
			}
		}
	});

	/** Scroll to the view to the element matching the given selector */
	function scrollToView(selector) {
		// Add a timeout so that if the element has just been shown (eg. via ngIf)
		// it is added to the DOM before we try to locate and scroll to it.
		scopeTimeout($scope, function() {
			scrollIntoView($document[0].querySelector(selector));
		}, 0);
	}

	/**
	 * Start the login flow. This will present the user with the login dialog.
	 *
	 * @return {Promise<void>} - A Promise that resolves when the login flow
	 *   completes. For non-OAuth logins, always resolves immediately.
	 */
	this.login = function() {

		if (this.auth.thirdPartyCookies) {
			if (serviceConfig(settings)) {
				// Let the host page handle the login request
				bridge.call(bridgeEvents.LOGIN_REQUESTED);
				return Promise.resolve();
			}

			return auth.login().then(() => {
				session.reload();
			}).catch((err) => {
				flash.error(err.message,{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
			});
		} else {
			frameSync.showSidebar();
			self.cookieDialog.visible = true;
		}
	};

	this.signUp = function() {
		analytics.track(analytics.events.SIGN_UP_REQUESTED);

		if (serviceConfig(settings)) {
			// Let the host page handle the signup request
			bridge.call(bridgeEvents.SIGNUP_REQUESTED);
			return;
		}
		$window.open(serviceUrl('signup'));
	};

    function share(annotationId) {
	    var group = groups.focused();
		if (group && group.objId && group.objClientId) {
			//var docMetaInfo = groups.getDocMetaInfo();
			var promise = groups.getMetaDataFromUrl();
		    promise.then(function(docMetaInfo) {
				if (group.webresource) {
					//docMetaInfo.metadata = group.webresource.metainfo;
					self.webresourceExist = true;
				}
				var obj = groups.getTsObject(group, group.objId, group.objClientId);
				if (!obj) {
					obj = {
						"type": "Document",
						"clientId": group.objClientId,
						"objectId": group.objId,
						"timestamp": null,
						"name": null,
						"objectInfo": null,
						"sourceUrl": null,
						"invalidObject": false
					};
				}
				if (!obj.name && docMetaInfo) {
					obj.name = docMetaInfo.title || docMetaInfo.uri;
					obj.sourceUrl = docMetaInfo.uri;
				}
				if (annotationId) {
					obj = angular.copy(obj);
					obj.digestDocAnnotId = annotationId;
				}
				getDocumentLink(group, obj);
		    });
		} else {
			flash.error("Please select the group",{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
		}
    }

	// Display the dialog for sharing the current page
	this.share = function(annotationId,data) {
		if (self.auth.status !== 'logged-out') {
			//self.shareDialog.visible = true;
			//scrollToView('share-dialog');
			//frameSync.setSidebarWidth(770);
			if(annotationId) {
				return share(annotationId);
			} else {
				var focusedGroup = groups.focused();
				if (focusedGroup.objId && focusedGroup.objId != null) {
					return share();
				}
			}
			
			addToTaskSpace(data).then(function(resp) {
				if (resp && !resp.message) {
					return share();
				}
			});
		} else {
			flash.error("You must login to share the Taskspace",{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
		}
	};

	


	function moveDocToTaskspace() {
		if (self.auth.status == 'logged-out') {
			return flash.error("You must login to move the web resource");
		}
		var focusedGroup = groups.focused();
		if (focusedGroup.objId && focusedGroup.objId != null) {
			moveWebResource.moveDocument(focusedGroup,self.auth);
		} else {
			flash.error('Focused group does not have web resource document on current url.',{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
		}
	}

	function authorize(authWindow) {
		var authResponse = new Promise((resolve, reject) => {
			function authRespListener(event) {
				if (event.data.code === 'Share Links') {
					resolve(event.data);
				}
				$window.removeEventListener('message', authRespListener);
			}
			$window.addEventListener('message', authRespListener);
		});
		return authResponse;
	}
	
	function closeShareWindow() {
		/*if (sharelinkWindow && sharelinkWindow != null && !sharelinkWindow.closed) {
			sharelinkWindow.close();
		}*/
		frameSync.closeShareLink();
	}

	function getDocumentLink(group, tsObj) {
		//closeShareWindow();
		if (group && tsObj) {
			var fileBaseUrl = self.auth.authority;
			var origin = "chrome-extension://" + self.auth.preferences.ExtensionId;
			var title = "SHARE ANNOTATIONS ON WEBPAGE";
			var sharelinkUrl = fileBaseUrl + "/sharelinks/" + group.id + "/" + group.clientId + "?d=" + tsObj.objectId + "&digestFor=DigestDocument" + "&origin=" + encodeURI(origin);
			if (!_.isEmpty(tsObj.digestDocAnnotId)) {
				title = "SHARE ANNOTATION ON WEBPAGE";
				sharelinkUrl = sharelinkUrl + "&da=" + tsObj.digestDocAnnotId;
			}
            sharelinkUrl =sharelinkUrl+"&title="+title;

            frameSync.shareLink(sharelinkUrl);
		} else {

		}
	}

	// Display the dialog for sharing the current page
	this.groupInfo = function() {
		if (self.auth.status !== 'logged-out') {
			self.groupInfoDialog.visible = !self.groupInfoDialog.visible;
			scrollToView('group-info-dialog');
		} else {
			flash.error("You must login to view the Taskspace information",{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
		}
	};
	
	// Display the taskspace digest in new window popup.
	this.viewTsDigest = function() {
		var fileBaseUrl = self.auth.authority;
		var origin = "chrome-extension://" + self.auth.preferences.ExtensionId;
		var group = groups.focused();
		if (group && group.objId && group.objClientId) {
			var viewTSDigestUrl = fileBaseUrl + "/tsdigestview/" + group.id + "/" + group.clientId + "?donotShowNavBar=true" + "&origin=" + encodeURI(origin);
			frameSync.viewTSDigest(viewTSDigestUrl);
		}
	};
	
	
	this.tagDocument = function(data) {
		if (store.focusedGroup()) {
			var focusedGroup = groups.focused();
			if (focusedGroup.objId && focusedGroup.objId != null) {
				$rootScope.$broadcast('showPageInfo',true);
				$rootScope.$broadcast('editTags',true);
			} else {
				store.selectTagBtn(true);
				addToTaskSpace(data).then(function(resp) {
					if (resp && !resp.message) {
						$rootScope.$broadcast('showPageInfo',true);
						$rootScope.$broadcast('editTags',true);
					}
				});
			}
		} else {
			if (!session.state.userid) {
				flash.error("Please Login to Save WebResource",{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
			} else {
				flash.error("Please Select a taskspace to Save WebResource",{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
			}
		}

	};

	// Display the dialog for create and share the new group/taskspace
	this.createGroup = function() {
		if (self.auth.status !== 'logged-out') {
			//self.createGroupDialog.visible = true;
			//scrollToView('create-group-dialog');
			
			groups.createTaskspace();
		} else {
			flash.error("You must login to create the Taskspace",{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
		}
	};

	this.showAboutPanel = function() {
		var service = serviceConfig(settings) || {};
		if (service.onAboutRequestProvided) {
			// Let the host page handle the about request.
			bridge.call(bridgeEvents.ABOUT_REQUESTED);
			return;
		}

		this.aboutPanel.visible = true;
	};

	this.showHelpPanel = function() {
		if (self.auth.status !== 'logged-out') {
			var service = serviceConfig(settings) || {};
			if (service.onHelpRequestProvided) {
				// Let the host page handle the help request.
				bridge.call(bridgeEvents.HELP_REQUESTED);
				return;
			}
			this.helpPanel.visible = true;
			scrollToView('help-panel');
		} else {
			flash.error("You must login to share the Taskspace",{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
		}
	};

	// Prompt to discard any unsaved drafts.
	var promptToLogout = function() {
		// TODO - Replace this with a UI which doesn't look terrible.
		var text = '';
		if (drafts.count() === 1) {
			text = 'You have an unsaved annotation.\n' +
				'Do you really want to discard this draft?';
		} else if (drafts.count() > 1) {
			text = 'You have ' + drafts.count() + ' unsaved annotations.\n' +
				'Do you really want to discard these drafts?';
		}
		return (drafts.count() === 0 || $window.confirm(text));
	};

	// Log the user out.
	this.logout = function() {
		if (!promptToLogout()) {
			return;
		}
		drafts.unsaved().forEach(function(draft) {
			$rootScope.$emit(events.ANNOTATION_DELETED, draft);
		});
		drafts.discard();

		if (serviceConfig(settings)) {
			// Let the host page handle the signup request
			bridge.call(bridgeEvents.LOGOUT_REQUESTED);
			return;
		}

		self.shareDialog = { visible: false };
		self.cookieDialog = { visible: false };
		//self.maintenanceDialog = { visible: false };
		self.groupInfoDialog = { visible: false };
		self.tagInfoDialog = { visible: false };
		self.createGroupDialog = { visible: false };
		self.aboutPanel = { visible: false };
		self.helpPanel = { visible: false };
        closeShareWindow();
		return session.logout();
	};

	this.showSignupBtn = function() {
		return (self.auth.isPublic && _.isEmpty(self.auth.userid));
	};

	this.search = {
		query: function() {
			return store.getState().filterQuery;
		},
		update: function(query) {
			store.setFilterQuery(query);
		},
	};

	this.countPendingUpdates = streamer.countPendingUpdates;
	this.applyPendingUpdates = streamer.applyPendingUpdates;

}

module.exports = {
	controller: HypothesisAppController,
	controllerAs: 'vm',
	template: require('../templates/hypothesis-app.html'),
};
