'use strict';

module.exports = ['$timeout', function($timeout) {
	return {
		link: function(scope, element, attrs) {
			function foucsElement(msg) {
				$timeout(function() {
					if (msg) {
						element[0].focus();
					} /*else {
						if (angular.isDefined(attrs.custAutoFocusLost)) {
							scope.$apply(attrs.ngFocusLost);
						}
					}*/
				});
			}
			
			scope.$on(attrs.name, function(event, msg) {
				foucsElement(msg);
			});
			
			/*scope.$watch(attrs.custAutoFocus, function(val) {
				if (angular.isDefined(val) && val) {
					foucsElement();
				}
			}, true);
			
			element.bind('blur', function() {
				if (angular.isDefined(attrs.custAutoFocusLost)) {
					scope.$apply(attrs.ngFocusLost);
				}
			});*/
		}
	};
}];
