'use strict';
var events = require('../events');
var urlUtil = require('../util/url-util');
var debounce = require('lodash.debounce');

// @ngInject
function PageInfoController($rootScope, $scope, groups, _, confirmDocRemoveAction, store,$timeout) {
	var self = this;
	var pageInfoTimer = null;
	this.editTitle = false;
	this.pageInfo = {};
	this.webresourceExist = false;
	this.hasEdit = false;
	this.showloader = true;
	this.tags = { editMode: store.getTagBtn(true) || false };
	
	$scope.$on("$destroy", function() {
        if (pageInfoTimer) {
           $timeout.cancel(pageInfoTimer);
        }
    });

	$scope.$on(events.GROUP_FOCUSED, function(event, focusedGroupId) {
		init();
	});

	$scope.$watch(() => ([groups.getwebResource()]), ([current], [prev]) => {
		init();
	}, true);

	$scope.$on("editTags", function(event, editmode) {
		self.tags.editMode = editmode;
	});

	$scope.$on("urlChanged", function(event, data) {
		init();
	});

	function removeConfirm(hasDocLink) {
		var text = "<div>The Document is Shared as a link. On removing the Document the link would be deleted permanently (shared link will not be accessible)</div>\
		<br><div>Are you sure you want to remove this document from task space ?</div>";
		var modalOptions = {
			hasDocLink: hasDocLink
		};
		return confirmDocRemoveAction.showModal({}, modalOptions);
	}

	function deleteDocument(group) {
		groups.deleteWebresource(group).then(function(resp) {
			if (resp) {
				groups.load(group.id);
			}
		});
	}

	function removeWebResource(group, deleteDoc, action) {
		groups.removeWebResource(group, action).then(function(resp) {
			if (resp) {
				if (deleteDoc) {
					deleteDocument(group);
				} else {
					groups.load(group.id);
				}
			}
		});
	}

	this.removeFromTaskSpace = function() {
		var group = groups.focused();
		if (!group) {
			return false;
		}
		let modalInstance = confirmDocRemoveAction.removeConfimation(group);
		modalInstance.then(function(userOptions) {
			removeWebResource(group, userOptions.deleteObject, userOptions.deleteAnnot);
		}, function() { });
	}

	this.addTotaskspace = function() {
		$rootScope.$broadcast(events.ADD_TO_TASKSPACE, {});
	};

	this.addPdfTotaskspace = function() {
		$rootScope.$broadcast(events.ADD_TO_TASKSPACE, {});
	};

	this.isPdf = false;
    
    function isPdfDocument() {
		self.isPdf = false;
		var docInfo = groups.fetchDocumentInfo();
		docInfo.then(docMetaInfo => {
			if (docMetaInfo && docMetaInfo.metadata) {
				if (docMetaInfo.metadata.filename || docMetaInfo.metadata.pdfInfo) {
					self.isPdf = true;
				} 
			} 
		});
	};

	this.canTag = function() {
		var group = groups.focused();
		return group && groups.canTag(group);
	};

	this.setPageInfo = function(metatags) {
		$timeout.cancel(pageInfoTimer);
		pageInfoTimer = $timeout(function(){
			self.pageInfo.title = metatags.title;
			self.pageInfo.imageUrl = metatags.imageUrl || "";
			self.pageInfo.description = metatags.description || "";
			self.pageInfo.sourceUrl = metatags.sourceUrl || "";
	
			if (self.pageInfo.sourceUrl) {
				var urlObj = urlUtil.parseUrl(self.pageInfo.sourceUrl);
				self.pageInfo.hostName = urlObj && urlObj.hostname ? urlObj.hostname : "";
			}
			self.showloader = false;
		},100);
	};

	function processMetaTagInfo(docMetaTags) {
		var metaInfo = {};
		if (docMetaTags) {
			if (null != docMetaTags["twitter:image"] && docMetaTags["twitter:image"] != "") {
				metaInfo["imageUrl"] = docMetaTags["twitter:image"];
			} else if (null != docMetaTags["og:image"] && docMetaTags["og:image"] != "") {
				metaInfo["imageUrl"] = docMetaTags["og:image"];
			} else if (null != docMetaTags["parsely-image-url"] && docMetaTags["parsely-image-url"] != "") {
				metaInfo["imageUrl"] = docMetaTags["parsely-image-url"];
			}

			if (null != docMetaTags["twitter:title"] && docMetaTags["twitter:title"] != "") {
				metaInfo["title"] = docMetaTags["twitter:title"];
			} else if (null != docMetaTags["og:title"] && docMetaTags["og:title"] != "") {
				metaInfo["title"] = docMetaTags["og:title"];
			} else if (null != docMetaTags["parsely-title"] && docMetaTags["parsely-title"] != "") {
				metaInfo["title"] = docMetaTags["parsely-title"];
			} else if (null != docMetaTags["webResourceTitle"] && docMetaTags["webResourceTitle"] != "") {
				metaInfo["title"] = docMetaTags["webResourceTitle"];
			}

			if (null != docMetaTags["twitter:url"] && docMetaTags["twitter:url"] != "") {
				metaInfo["sourceUrl"] = docMetaTags["twitter:url"];
			} else if (null != docMetaTags["og:url"] && docMetaTags["og:url"] != "") {
				metaInfo["sourceUrl"] = docMetaTags["og:url"];
			} else if (null != docMetaTags["parsely-link"] && docMetaTags["parsely-link"] != "") {
				metaInfo["sourceUrl"] = docMetaTags["parsely-link"];
			}

			if (null != docMetaTags["twitter:description"] && docMetaTags["twitter:description"] != "") {
				metaInfo["description"] = docMetaTags["twitter:description"];
			} else if (null != docMetaTags["og:description"] && docMetaTags["og:description"] != "") {
				metaInfo["description"] = docMetaTags["og:description"];
			} else if (null != docMetaTags["description"] && docMetaTags["description"] != "") {
				metaInfo["description"] = docMetaTags["description"];
			} else if (null != docMetaTags["Description"] && docMetaTags["Description"] != "") {
				metaInfo["description"] = docMetaTags["Description"];
			}

			if (null != docMetaTags["twitter:image:width"] && docMetaTags["twitter:image:width"] != "") {
				metaInfo["imageWidth"] = docMetaTags["twitter:image:width"];
			} else if (null != docMetaTags["og:image:width"] && docMetaTags["og:image:width"] != "") {
				metaInfo["imageWidth"] = docMetaTags["og:image:width"];
			}

			if (null != docMetaTags["twitter:image:height"] && docMetaTags["twitter:image:height"] != "") {
				metaInfo["imageHeight"] = docMetaTags["twitter:image:height"];
			} else if (null != docMetaTags["og:image:height"] && docMetaTags["og:image:height"] != "") {
				metaInfo["imageHeight"] = docMetaTags["og:image:height"];
			}
		}

		return metaInfo;
	}

	var init = debounce(() => {
		self.showloader = true;
		self.pageInfo = {};
		self.webresourceExist = false;
		self.hasEdit = false;
		self.tags.editMode = store.getTagBtn(true) || false;
		var group = groups.focused();
		var metatags;
		if (group) {
			try {
				self.hasEdit = group.permissions.edit;
			} catch (e) { }
			if (group.webresource) {
				metatags = group.webresource.metainfo;
				self.webresourceExist = true;
				if (metatags) {
					self.setPageInfo(metatags);
				}
			} else {
				var promise = groups.getMetaDataFromUrl();
				promise.then(function(docMetaInfo) {
					if (docMetaInfo && docMetaInfo.metadata) {
						if (docMetaInfo.metadata.metatags) {
							metatags = processMetaTagInfo(docMetaInfo.metadata.metatags);
						} else if (docMetaInfo.metadata.filename || docMetaInfo.metadata.pdfInfo) {
							metatags = {
								title: docMetaInfo.metadata.title || docMetaInfo.metadata.filename,
								sourceUrl: docMetaInfo.uri || "",
							};
						}
						metatags.title = metatags.title || (docMetaInfo.metadata ? docMetaInfo.metadata.title : "") || docMetaInfo.uri;
					}
					if (metatags) {
						self.setPageInfo(metatags);
					}
				});
			}
			isPdfDocument();
		}
	}, 1000);

	init();
}

module.exports = {
	controller: PageInfoController,
	controllerAs: 'vm',
	bindings: {
		auth: '<'
	},
	template: require('../templates/page-info.html'),
};
