'use strict';

/**
 * A service for displaying "confirm"  messages.
 */

// @ngInject
function MoveConfirmation($modal) {
	
	var modalOptions = {
		selectedGroup: {},
		cancel: 'Cancel',
		ok: 'Submit',
		headerText: 'Are you sure you want to move the Document ?'
	};
	this.modalInstance = null;
	this.confirm = function (confirmdata) {
		  if(this.modalInstance) {
			this.modalInstance.dismiss("cancel");
			this.modalInstance = null;
		  }

	      this.modalInstance = $modal.open({
				  animation: true,
				  template: require('../templates/move-confirmation.html'),
				  controller: 'MoveDocConfirmModal',
				  controllerAs: 'vm',
				  backdrop: 'static',
				  size : 'lg',
				  resolve: {
					  confirmdata :function() {
						  return confirmdata;
					  },
			          modalOptions: function() {
					  	  return modalOptions;
					  }
				  }
			});
			return this.modalInstance.result;
	};
}

module.exports = MoveConfirmation;