'use strict';
var events = require('../events');
const memoize = require('../util/memoize');
const groupOrganizations = memoize(require('../util/group-organizations'));
// @ngInject
function SelectTaskspace( groups,frameSync,userPreference) {
  'ngInject';
  var self= this;
  this.groups = groups;
  this.selected = {
	taskspace: self.group
  };
  function init() {
	  getAllTaskspaces();
  }
   
  this.selectedTaskspace = function(group) { 
	this.selected.taskspace = group;
  };

  var getAllTaskspaces = function () {
	var taskspaces = groups.all();
	if(taskspaces) {
		taskspaces = taskspaces.filter(function(ts) {
			return groups.isEditable(ts);
		});
	}
    self.allTaskspaces = groupOrganizations(taskspaces);
  };

  this.isSelectable = function(group) {
	return groups.isSelectable(group);
  };
  
  this.focusTaskspace = function (group) {
    this.group = group;
  };
 
  this.allowCreateTaskSpace = userPreference.canCreateTaskspace;
  
  this.selectTaskspace = function() {
	  return (this.auth.status === 'logged-in' && !this.groups.focused());
  }
  
  this.addNewTaskspace = function() {
	let tsSearchTxt = "";
	if(self.uiSelect) {
		tsSearchTxt = self.uiSelect.search;
	}
	let newTsModal = groups.createTaskspace(tsSearchTxt);
	newTsModal.then(function(ts) {
		getAllTaskspaces();
	},function() {
		
	});
  }

  init();
}

module.exports = {
  controller: SelectTaskspace,
  controllerAs: 'vm',
  bindings: {
    auth: '<',
    group:'='
  },
  template: require('../templates/select-taskspace.html'),
};


