'use strict';

var queryString = require('query-string');
var uuid = require('node-uuid');

var events = require('../events');
var Socket = require('../websocket');



/*LOGOUT_NOTIFICATION,
		
		COMMENT_CREATED_OR_UPDATED,
		COMMENT_DELETED,
		
		DOCS_ADDED,
		DOCS_REMOVED,
		DOCUMENT_MOVED,
		
		
		SHARED,
		UNSHARED,
		PERMISSIONS_CHANGED,
		
		SECTION_ADDED,
		SECTION_REMOVED,
		SECTION_RENAMED,
		
		SHEDULED_MAINTENANCE,
		SHEDULED_SHUTDOWN,
		
		TS_DELETED,
		TS_CREATED,
		TS_RENAMED,
		TS_OWNER_CHANGED,
		
		MSOFFICE_AUTH_UPDATE,
		LINKEDIN_AUTH_UPDATE,*/

/**
 * Open a new WebSocket connection to the Hypothesis push notification service.
 * Only one websocket connection may exist at a time, any existing socket is
 * closed.
 *
 * @param $rootScope - Scope used to $apply() app state changes
 *                     resulting from WebSocket messages, in order to update
 *                     appropriate watchers.
 * @param annotationMapper - The local annotation store
 * @param groups - The local groups store
 * @param session - Provides access to read and update the session state
 * @param settings - Application settings
 */
// @ngInject
function Streamer($rootScope, annotationMapper, store, auth,
	groups, session, settings, flash, extCommunicator, confirm,_) {
	// The randomly generated session UUID
	var clientId = uuid.v4();

	// The socket instance for this Streamer instance
	var socket;

	// Client configuration messages, to be sent each time a new connection is
	// established.
	var configMessages = {};

	// The streamer maintains a set of pending updates and deletions which have
	// been received via the WebSocket but not yet applied to the contents of the
	// app.
	//
	// This state should be managed as part of the global app state in
	// store, but that is currently difficult because applying updates
	// requires filtering annotations against the focused group (information not
	// currently stored in the app state) and triggering events in order to update
	// the annotations displayed in the page.

	// Map of ID -> updated annotation for updates that have been received over
	// the WS but not yet applied
	var pendingUpdates = {};
	var pendingGroupCounts = {};
	// Set of IDs of annotations which have been deleted but for which the
	// deletion has not yet been applied
	var pendingDeletions = {};
	
	function getTaskspaceUpdateMessage(message) {
		var text = "";
		var key = message.action;
		if (key == "DOCS_ADDED") {
			text = text + message.userId + ' Added a Document to the taskspace \n';
		}
		if (key == "SECTION_ADDED") {
			text = text + message.userId + ' Added a Section to the taskspace \n';
		}
		if (key == "SECTION_REMOVED") {
			text = text + message.userId + ' Removed a Section from the taskspace \n';
		}
		if (key == "SECTION_RENAMED") {
			text = text + message.userId + ' Renamed a Taskspace Section  \n';
		}

		if (key == "COMMENT_CREATED_OR_UPDATED") {
			text = text + message.userId + ' Added a comment \n';
		}

		if (key == "COMMENT_DELETED") {
			text = text + message.userId + ' Deleted a comment \n';
		}

		if (key == "DOCS_REMOVED") {
			text = text + message.userId + ' Removed a document from the taskspace \n';
		}

		if (key == "TS_RENAMED") {
			text = text + message.userId + ' Renamed the taskspace \n';
		}
		if (key == "TS_DELETED") {
			text = text + message.userId + ' Deleted the taskspace \n';
		}
		if (key == "TS_CREATED") {
			text = text + message.userId + ' Added a taskspace \n';
		}
		if (key == "TS_OWNER_CHANGED") {
			text = text + message.userId + ' Changed  the taskspace Owner \n';
		}

		if (key == "DOCUMENT_MOVED") {
			text = text + message.userId + ' Moved the taskspace Document \n';
		}

		if (key == "SHARED") {
			text = text + message.userId + ' Shared the taskspace \n';
		}
		
		if(key == "PERMISSIONS_CHANGED") {
			text = text + message.userId + ' changed the taskspace permissions \n';
		}

		if (key == "UNSHARED") {
			text = text + message.userId + ' Unshared the taskspace \n';
		}
		return text;
	}

    function ownNotification(message) {
		 var currentUser = store.profile() && store.profile().userid ? store.profile().userid : "";
		 if (message.userId && currentUser && "acct:"+message.userId.toLowerCase() == currentUser.toLowerCase()) {
			return true;
		 }
	     return false;
    }

	function handleAnnotationNotification(message) {
		var action = message.action;
		var annotations = message.data ? [message.data] : [];

		switch (action) {
			case 'COMMENT_CREATED_OR_UPDATED':
				annotations.forEach(function(ann) {
					// In the sidebar, only save pending updates for annotations in the
					// focused group, since we only display annotations from the focused
					// group and reload all annotations and discard pending updates
					// when switching groups.
					if (ann.group === groups.focusedId() || !store.isSidebar()) {
						pendingUpdates[ann.id] = ann;
					}
				});
				break;
			case 'COMMENT_DELETED':
				annotations.forEach(function(ann) {
					// Discard any pending but not-yet-applied updates for this annotation
					delete pendingUpdates[ann.id];

					// If we already have this annotation loaded, then record a pending
					// deletion. We do not check the group of the annotation here because a)
					// that information is not included with deletion notifications and b)
					// even if the annotation is from the current group, it might be for a
					// new annotation (saved in pendingUpdates and removed above), that has
					// not yet been loaded.
					if (store.annotationExists(ann.id)) {
						pendingDeletions[ann.id] = true;
					}
				});
				break;
		}

		var focusedGroup = groups.focused();
		if (focusedGroup) {
			pendingGroupCounts[focusedGroup.id] = message.count;
		}

		if (!store.isSidebar()) {
			applyPendingUpdates();
		}
	}

	function handleSessionChangeNotification(message) {
		session.update(message.model);
		groups.load();
	}

	function handleGroupNotification(message) {
		groups.load();
		extCommunicator.sendmessage({ action: "badgeCount" });
		if (message["notification-message"]) {
			flash.info(message["notification-message"]);
		}
		showMessage(message);
	}

	function handleGroupUpdated(message) {
		if (!message.tsId && !message.tsClientId) {
			return null;
		}
		groups.getTaskspace(message.tsId, message.tsClientId).then(gp => {
			if (gp) {
				store.replaceGroup(gp.id, gp);
				extCommunicator.sendmessage({ action: "badgeCount" });
				showMessage(message);
			}
		});
		$rootScope.$broadcast(events.GROUP_UPDATED,message.tsId);
	}

   function handleDocRemoved(message) {
		if (!message.tsId && !message.tsClientId) {
			return null;
		}
		if (!ownNotification(message) && message.tsId === groups.focusedId() || !store.isSidebar()) {
			var gp = groups.focused();
			if(gp && message.docIds &&  message.docIds.indexOf(gp.objId) != -1) {
				confirmRemove(message).finally(function() {
					removeDoc(message);
				});
			}
		} else {
			removeDoc(message);
			showMessage(message);
		}
   }
   

    function showMessage(message) {
		var notificationMessage = getTaskspaceUpdateMessage(message);
		if(!notificationMessage && ownNotification(message)) {
			return;
		}
		flash.info(notificationMessage);
	}

	function handleNewGroup(message) {
		if (!message.tsId && !message.tsClientId) {
			return null;
		}
		groups.getTaskspace(message.tsId, message.tsClientId).then(gp => {
			if (gp) {
				var gs = store.allGroups();
				if (gs && gs.length == 0) {
					return;
				}
				const group = gs.find(g => g.id === gp.id);
				if (group) {
					var allgroups = gs.map(function(group) {
						if (group.id !== gp.id) {
							return group;
						}
						return Object.assign({}, group, gp);
					});
					store.loadGroups(allgroups);
				} else {
					var allgroups = gs.map(function(group) {
						return group;
					});
					allgroups = allgroups ? allgroups.concat([gp]) : [gp];
					store.loadGroups(allgroups);
				}
			    showMessage(message);
                extCommunicator.sendmessage({ action: "badgeCount" });
                $rootScope.$broadcast(events.GROUP_UPDATED,message.tsId);
			}
		});
	}

    function removeGroup(id) {
		var gs = store.allGroups();
		if(gs && gs.length == 0) {
			return;
		}
		var allgroups = gs.filter(function(group) {
			return group.id !== id;
		});
		allgroups = allgroups ? allgroups : [];
		store.loadGroups([]);
		groups.loadAllGroups(allgroups);
    }

     function removeDoc(message) {
		groups.getTaskspace(message.tsId, message.tsClientId).then(gp => {
			if (gp) {
				store.replaceGroup(gp.id, gp);
				extCommunicator.sendmessage({ action: "badgeCount" });
				showMessage(message);
			}
		});
		$rootScope.$broadcast(events.GROUP_UPDATED,message.tsId);
    }



	function handleRemoveGroupNotification(message) {
		if (!message.tsId && !message.tsClientId) {
			return null;
		}
		if (!ownNotification(message) && message.tsId === groups.focusedId() || !store.isSidebar()) {
			confirmRemove(message).finally(function() {
				removeGroup(message.tsId );
			});
		} else {
			removeGroup(message.tsId );
			showMessage(message);
		}
	}

   function isNotificationForMe(message) {
	var status = false;
	if(message.collaborators) {
		var currentUser = store.profile() && store.profile().userid ? store.profile().userid : "";
		var user = _.find(message.collaborators, function(val) {
			return currentUser && "acct:"+val.toLowerCase() == currentUser.toLowerCase();
		});
		if(user) {
			status = true;
		}
	}
	
	return status;
   }

   function handleUnshareGroupNotification(message) {
		if (!message.tsId && !message.tsClientId) {
			return null;
		}
		if (!ownNotification(message) && isNotificationForMe(message)  || !store.isSidebar()) {
			if(message.tsId === groups.focusedId()) {
				confirmRemove(message).finally(function() {
					removeGroup(message.tsId );
				});
			} else {
				removeGroup(message.tsId );
				showMessage(message);
			}
		} else {
			groups.getTaskspace(message.tsId, message.tsClientId).then(gp => {
				if (gp) {
					store.replaceGroup(gp.id, gp);
					showMessage(message);
					$rootScope.$broadcast(events.GROUP_UPDATED,message.tsId);
				}
			});
		}
   }
   
   function handleShareGroupNotification(message) {
		if (!message.tsId && !message.tsClientId) {
			return null;
		}
		if ( isNotificationForMe(message)) {
			handleNewGroup(message);
		} else {
			groups.getTaskspace(message.tsId, message.tsClientId).then(gp => {
				if (gp) {
					store.replaceGroup(gp.id, gp);
					showMessage(message);
					$rootScope.$broadcast(events.GROUP_UPDATED,message.tsId);
				}
			});
		}
   }

   function confirmRemove(message) {
		var notificationMessage = getTaskspaceUpdateMessage(message);
		var msg = notificationMessage;
		var modalOptions = {
			closeButtonText: 'Cancel',
			actionButtonText: 'Ok',
			headerText: 'Notification',
			bodyText: msg
		};
		return confirm.showModal({}, modalOptions);
	}

	function handleLinkedInAuthNotification(message) {
		$rootScope.$broadcast('LinkedInAuthNotification', message.options);
	}

	function handleScheduleMaintenaceNotification(message) {
		$rootScope.$broadcast(message.action, message.options);
	}
	function handleSocketOnError(event) {
		console.warn('Error connecting to Numici push notification service:', event);

		// In development, warn if the connection failure might be due to
		// the app's origin not having been whitelisted in the H service's config.
		//
		// Unfortunately the error event does not provide a way to get at the
		// HTTP status code for HTTP -> WS upgrade requests.
		var websocketHost = new URL(settings.websocketUrl).hostname;
		if (['localhost', '127.0.0.1'].indexOf(websocketHost) !== -1) {
			console.warn('Check that your Numici service is configured to allow ' +
				'WebSocket connections from ' + window.location.origin);
		}
	}

	function handleSocketOnMessage(event) {
		// Wrap message dispatches in $rootScope.$apply() so that
		// scope watches on app state affected by the received message
		// are updated
		$rootScope.$apply(function() {
			var message = JSON.parse(event.data);
			if (!message) {
				return;
			}

			if (message.action === 'COMMENT_CREATED_OR_UPDATED' || message.action === 'COMMENT_DELETED') {
				handleAnnotationNotification(message);
				return;
			}
			if (message.type === 'session-change') {
				handleSessionChangeNotification(message);
				return;
			}

			if (message.action === 'TS_DELETED') {
				handleRemoveGroupNotification(message);
				return;
			}

            if (message.action === 'SHARED') {
				handleShareGroupNotification(message);
				return;
			}
			
			if (message.action === 'UNSHARED') {
				handleUnshareGroupNotification(message);
				return;
			}


			if (message.action === 'SECTION_ADDED'
				|| message.action === 'SECTION_REMOVED'
				|| message.action === 'SECTION_RENAMED'
				|| message.action === 'TS_OWNER_CHANGED'
				|| message.action === 'TS_RENAMED'
				|| message.action === 'PERMISSIONS_CHANGED') {
				handleGroupUpdated(message);
				return;
			}

			if (message.action === 'TS_CREATED') {
				handleNewGroup(message);
				return;
			}

			if (message.action === 'DOCS_ADDED') {
				handleGroupUpdated(message);
				return;
			}

			if (message.action === 'DOCS_REMOVED') {
				handleDocRemoved(message);
				return;
			}

			if (message.action === 'DOCUMENT_MOVED') {
				handleGroupUpdated(message);
				return;
			}

			if (message.type === 'whoyouare') {
				var userid = store.getState().session.userid;
				if (userid && message.userid !== userid) {
					console.warn('WebSocket user ID "%s" does not match logged-in ID "%s"', message.userid, userid);
				}
				return;
			}
			if (message.type === 'LOGOUT_NOTIFICATION') {
				session.logout();
				return;
			}
			if (message.type === 'LINKEDIN_AUTH_UPDATE') {
				handleLinkedInAuthNotification(message);
				return;
			}
			if (message.action === 'SHEDULED_MAINTENANCE' || message.action === "SHEDULED_SHUTDOWN") {
				handleScheduleMaintenaceNotification(message)
				return;
			}


			console.warn('received unsupported notification', message.type);
		});
	}

	function sendClientConfig() {
		Object.keys(configMessages).forEach(function(key) {
			if (configMessages[key]) {
				socket.send(configMessages[key]);
			}
		});
	}

	/**
	 * Send a configuration message to the push notification service.
	 * Each message is associated with a key, which is used to re-send
	 * configuration data to the server in the event of a reconnection.
	 */
	function setConfig(key, configMessage) {
		configMessages[key] = configMessage;
		if (socket && socket.isConnected()) {
			socket.send(configMessage);
		}
	}

	var _connect = function() {
		// If we have no URL configured, don't do anything.
		if (!settings.websocketUrl) {
			return Promise.resolve();
		}

		return auth.tokenGetter().then(function(token) {
			var url;
			if (token) {
				// Include the access token in the URL via a query param. This method
				// is used to send credentials because the `WebSocket` constructor does
				// not support setting the `Authorization` header directly as we do for
				// other API requests.
				var parsedURL = new URL(settings.websocketUrl);
				var queryParams = queryString.parse(parsedURL.search);
				queryParams.access_token = token;
				parsedURL.search = queryString.stringify(queryParams);
				url = parsedURL.toString();
			} else {
				url = settings.websocketUrl;
			}

			socket = new Socket(url);

			socket.on('open', sendClientConfig);
			socket.on('error', handleSocketOnError);
			socket.on('message', handleSocketOnMessage);

			// Configure the client ID
			setConfig('client-id', {
				messageType: 'client_id',
				value: clientId,
			});

			// Send a "whoami" message. The server will respond with a "whoyouare"
			// reply which is useful for verifying that authentication worked as
			// expected.
			setConfig('auth-check', {
				type: 'whoami',
				id: 1,
			});
		}).catch(function(err) {
			console.error('Failed to fetch token for WebSocket authentication', err);
		});
	};

	/**
	 * Connect to the Hypothesis real time update service.
	 *
	 * If the service has already connected this does nothing.
	 *
	 * @return {Promise} Promise which resolves once the WebSocket connection
	 *                   process has started.
	 */
	function connect() {
		if (socket) {
			return Promise.resolve();
		}

		return _connect();
	}

	/**
	 * Connect to the Hypothesis real time update service.
	 *
	 * If the service has already connected this closes the existing connection
	 * and reconnects.
	 *
	 * @return {Promise} Promise which resolves once the WebSocket connection
	 *                   process has started.
	 */
	function reconnect() {
		if (socket) {
			socket.close();
		}

		return _connect();
	}

	function applyPendingUpdates() {
		var updates = Object.values(pendingUpdates);
		var deletions = Object.keys(pendingDeletions).map(function(id) {
			return { id: id };
		});

		if (updates.length) {
			annotationMapper.loadAnnotations(updates);
		}
		if (deletions.length) {
			annotationMapper.unloadAnnotations(deletions);
		}
		if (Object.keys(pendingGroupCounts).length !== 0) {
			groups.updateCount(pendingGroupCounts)
		}
		pendingUpdates = {};
		pendingDeletions = {};
		pendingGroupCounts = {};
	}

	function countPendingUpdates() {
		return Object.keys(pendingUpdates).length +
			Object.keys(pendingDeletions).length;
	}

	function hasPendingDeletion(id) {
		return pendingDeletions.hasOwnProperty(id);
	}

	function removePendingUpdates(event, anns) {
		if (!Array.isArray(anns)) {
			anns = [anns];
		}
		anns.forEach(function(ann) {
			delete pendingUpdates[ann.id];
			delete pendingDeletions[ann.id];
		});
	}

	function clearPendingUpdates() {
		pendingUpdates = {};
		pendingDeletions = {};
	}

	var updateEvents = [
		events.ANNOTATION_DELETED,
		events.ANNOTATION_UPDATED,
		events.ANNOTATIONS_UNLOADED,
	];

	updateEvents.forEach(function(event) {
		$rootScope.$on(event, removePendingUpdates);
	});
	$rootScope.$on(events.GROUP_FOCUSED, clearPendingUpdates);

	this.applyPendingUpdates = applyPendingUpdates;
	this.countPendingUpdates = countPendingUpdates;
	this.hasPendingDeletion = hasPendingDeletion;
	this.clientId = clientId;
	this.configMessages = configMessages;
	this.connect = connect;
	this.reconnect = reconnect;
	this.setConfig = setConfig;
}

module.exports = Streamer;
