'use strict';

var renderMarkdown = require('../render-markdown');
/**
 * A service for displaying "confirm"  messages.
 */

// @ngInject
function ShareLinkModal($scope,api,$modal,$modalInstance,sharelinks,settings,authUserPreferences,linkInfo,digestFor,authUserContext,confirm,_,flash,$timeout,$compile,$templateCache,$window,$sanitize) {
	var self = this;
	var config = {};
	var sharedUserList = [];
	var selectedProvisionalUsers = [];
	
	self.digestFor = digestFor.digestFor;
	self.selectedPublishAsNote = false;
	self.selectedPublishAsPdf = false;
	self.selectedPublishAsLink = false;
	self.showdocLink = false;
	self.digestName = digestFor.digestName;
	self.noteName = "";
	self.pdfName = "";
	self.emailUsers = [];
	self.emailSubject = "";
	
	self.digestDescription = "";
	self.linkInfo = angular.copy(linkInfo);
	self.linkType = {"type" : "Public"};
	self.linkTarget = {"target" : "Document"};
	self.focusLink = false;
	self.filterOptions = {};
	self.digestMetaInfoOptions = {};
	self.groupByOptionList = angular.copy(sharelinks.getGroupByUIOptions());
	self.groupByOption = self.groupByOptionList[0];
	self.enableBorderOptionList = angular.copy(sharelinks.getEnableBorderUIOptions());
	self.enableBorderOption = self.enableBorderOptionList[0];
	self.imagePositionOptionList = angular.copy(sharelinks.getImagePositionUIOptions());
	self.imagePositionOption = self.imagePositionOptionList[0];
	self.displayOrderOptionList = angular.copy(sharelinks.getDisplayOrderUIOptions());
	self.displayOrderOption = self.displayOrderOptionList[0];
	self.displayRepliesOptionList = angular.copy(sharelinks.getDisplayRepliesUIOptions());
	self.filterOptions.displayRepliesOption = self.displayRepliesOptionList[0];
	self.selectedTemplate = {};
	
	self.tags =[];
	
    self.docHierarchy = [];
	
    self.digestNameError = false;
	self.digestNameErrorMessage = "";
	
	self.cancel = cancel;
	self.copySuccess = copySuccess;
	self.copyFail = copyFail;
	self.ShareOnSocialMedia = ShareOnSocialMedia;
	self.updateDigestLink = updateDigestLink;
	self.checkAndPublishAsSelectedType = checkAndPublishAsSelectedType;
	self.cancelsaveAsSelectedType = cancelsaveAsSelectedType;
	self.saveAsSelectedType = saveAsSelectedType;
	self.formatCreatedDate = sharelinks.formatCreatedDate;
	var fileBaseUrl = authUserContext.path;
	self.commentIconUrl = fileBaseUrl+"/app/assets/icons/digest_comment.png";
	
	self.setDigesMinMaxWidth = sharelinks.setDigesMinMaxWidth;
	self.getTitleStyles = sharelinks.getTitleStyles;
	self.alternateImageStyles = sharelinks.alternateImageStyles;
	self.setRepDigestStyles = sharelinks.setRepDigestStyles;
	var trustedAnnotatedText = {};
	
	self.getAnnotatedText = function (annotation) {
		if(!trustedAnnotatedText[annotation.annotationId]) {
			trustedAnnotatedText[annotation.annotationId] = sharelinks.getAnnotatedText(annotation,self.digestMetaInfoOptions);
		}
		return trustedAnnotatedText[annotation.annotationId];
	};
	
	self.formatCreatedDate = sharelinks.formatCreatedDate;
	self.formatComment = sharelinks.formatComment;
	
	self.getTitle = getTitle;
	self.annotationsDigestSettings = annotationsDigestSettings;
	self.getLinkTypeMsg = getLinkTypeMsg;
	self.getChangeToLinkType = getChangeToLinkType;
	self.showPublishAs = showPublishAs;
	
	function getTitle() {
		if(digestFor.documentInfo && digestFor.documentInfo.digestDocAnnotId ) {
			return "SHARE ANNOTATION ON WEBPAGE";
		}
		return "SHARE ANNOTATIONS ON WEBPAGE";
	}
	
	function handleDigestSettingsResp(digestSettingsRespData) {
		if(!_.isEmpty(digestSettingsRespData)) {
			if (digestSettingsRespData.type === 'digest_settings_update_resp') {
				self.linkInfo = digestSettingsRespData.data;
				init();
				$scope.$apply();
		    }
			if (digestSettingsRespData.type === 'digest_settings_delete_resp') {
				cancel();
		    }
		}
	}
	
	function annotationsDigestSettings() {
		var origin = "chrome-extension://"+authUserPreferences.ExtensionId;
		var digestSettingsUrl = fileBaseUrl+"/digestsettings/"+settings.taskspaceId+"/"+settings.clientId+"?d="+digestFor.documentInfo.objectId+"&digestFor="+digestFor.digestFor+"&origin="+origin;
		if(!_.isEmpty(digestFor.documentInfo.digestDocAnnotId)) {
			digestSettingsUrl = digestSettingsUrl+"&da="+digestFor.documentInfo.digestDocAnnotId;
		}
		
		var digestSettingsWindow = sharelinks.openJSPopupWindow("Digest Settings");
		digestSettingsWindow.location = digestSettingsUrl;
		authorize(digestSettingsWindow).then(respData => {
			if (respData) {
				if(respData.state == "Success") {
					handleDigestSettingsResp(respData);
				}
			}
		});
	}
	
	function getLinkTypeMsg(type) {
		var objectType = "webpage";
		if(type == "Public") {
			return "Public Link: Anyone with the Link can view the annotations on the "+objectType;
		} else if(type == "Private") {
			return "Private Link: Only users with access to the Taskspace can view the annotations on the "+objectType;
		}
	}
	
	function getChangeToLinkType(type) {
		if(type == "Public") {
				return "private";
		} else if(type == "Private") {
			return "public";
		}
	}
	
	function showPublishAs() {
		var status = false;
		if(self.digestFor == 'DigestDocument' && (digestFor.documentInfo && !digestFor.documentInfo.digestDocAnnotId)) {
			status = true;
		}
		return status;
	}
	
	function getShareCommentaryTextForSocialMedia(cb) {
		sharelinks.getDigestShareCommentaryTxt(digestFor,self.digestDescription,self.filterOptions,self.displayOrderOption,self.linkInfo.dlId,settings.clientId,function(shareCommentaryTxt){
			if(typeof cb == "function") {
				cb(shareCommentaryTxt);
			}
		});
	}
	
	function setErrorMsg (field,type,Message) {
		if(field === "digestName" && type === "invalid") {
			self.digestNameError = true;
			self.digestNameErrorMessage = "Please Enter Digest Name.";
		}
	}
	
	function configDigestDataFromDigestLink() {
		if(!_.isEmpty(self.linkInfo.properties)) {
			config = self.linkInfo.properties.digestSettings;
		} else if(!_.isEmpty(self.linkInfo.linkObj) && !_.isEmpty(self.linkInfo.linkObj.properties)) {
			config = self.linkInfo.linkObj.properties.digestSettings;
		} else {
			config = _.isEmpty(settings.digestsettings) ? sharelinks.getDigestFilters() : settings.digestsettings;
		}
		if(!_.isEmpty(self.linkInfo.linkObj)) {
			self.digestName = self.linkInfo.linkObj.properties.digestSettings.digestName;
			self.noteName = angular.copy(self.digestName);
			self.pdfName = angular.copy(self.digestName);
			self.emailSubject = angular.copy(self.digestName);
			self.digestDescription = self.linkInfo.linkObj.properties.digestSettings.digestDescription
			self.linkType.type = angular.copy(self.linkInfo.linkObj.linkType);
			if(!_.isEmpty(self.linkInfo.linkObj.toEmailIds)) {
				self.emailUsers = angular.copy(self.linkInfo.linkObj.toEmailIds);
			}
			self.linkTarget.target = angular.copy(self.linkInfo.linkObj.linkTarget) || "Digest ";
			self.selectedPublishAsLink = true;
		}
		self.filterOptions = angular.copy(config.filterOptions);
		self.digestMetaInfoOptions = angular.copy(config.digestMetaInfoOptions);
		//self.filterOptions.startDate = moment(self.filterOptions.startDate,defautlDateFormat,true).isValid() ? moment(self.filterOptions.startDate,defautlDateFormat).toDate() : null;
		//self.filterOptions.endDate =  moment(self.filterOptions.endDate,defautlDateFormat,true).isValid() ? moment(self.filterOptions.endDate,defautlDateFormat).toDate() : null;
		var enableBorderOptionIndex = _.findIndex(self.enableBorderOptionList,{"value" : config.enableBorder});
		if(enableBorderOptionIndex != -1) {
			self.enableBorderOption = self.enableBorderOptionList[enableBorderOptionIndex];
		}
		var imagePositionIndex = _.findIndex(self.imagePositionOptionList,{"value" : config.imagePosition});
		if(imagePositionIndex != -1) {
			self.imagePositionOption = self.imagePositionOptionList[imagePositionIndex];
		}
		var displayOrderIndex = _.findIndex(self.displayOrderOptionList,{"value" : config.displayOrder});
		if(displayOrderIndex != -1) {
			self.displayOrderOption = self.displayOrderOptionList[displayOrderIndex];
		}
		var displayRepliesIndex = _.findIndex(self.displayRepliesOptionList,{"value" : config.filterOptions.displayReplies});
		if(displayRepliesIndex != -1) {
			self.filterOptions.displayRepliesOption = self.displayRepliesOptionList[displayRepliesIndex];
		}
	}
	
	function cancel() {
		$modalInstance.dismiss('cancel');
	}
	
	function preProcessDigestDataForLink() {
		var filterOptions = angular.copy(self.filterOptions);
		
		filterOptions.displayReplies = filterOptions.displayRepliesOption.value;
		delete filterOptions.displayRepliesOption;
		
		filterOptions["allTagAnnotations"] = true;
		var groupBy = self.groupByOption ? self.groupByOption.value : null;
		
		var digestSettings = {
				"digestName": self.digestName, 
				"digestDescription": self.digestDescription,
				"objectId": settings.taskspaceId, 
				"clientId": settings.clientId,
				"context" : "taskspace",
				"filterOptions" : filterOptions,
				"groupBy": self.groupByOption.value,
				"digestMetaInfoOptions": self.digestMetaInfoOptions,
				"selectedTemplate": self.selectedTemplate ? self.selectedTemplate.id : null,
				"enableBorder" : self.enableBorderOption.value,
				"imagePosition" : self.imagePositionOption.value,
				"displayOrder" : self.displayOrderOption.value
		};
		
		if (digestFor.digestFor == "DigestDocument") {
			if (digestFor.documentInfo) {
				digestSettings.documents = [digestFor.documentInfo.objectId];
			}
		}
		
		return digestSettings;
	}
	
	function updateLink(linkObj,clientId) {
		return DeepLinkService.updateLink(linkObj,clientId);
	}
	
	function updateDigestDocAnnot(linkObj,clientId) {
		return DeepLinkService.updateDigestDocAnnot(linkObj);
	}
	
	function updateDigestLink(toEmailIds,cb) {
		var digestSettings = preProcessDigestDataForLink();
		var lnk = angular.copy(self.linkInfo);
		var updateLinkPromise;
		lnk.linkObj.properties.digestSettings.digestName = self.digestName;
		lnk.linkObj.properties.digestSettings.digestDescription = self.digestDescription;
		if(toEmailIds) {
			lnk.linkObj.toEmailIds = toEmailIds;
		}
		lnk.linkObj.linkType = self.linkType.type;
		lnk.linkObj.linkTarget = self.linkTarget.target;
		lnk.linkObj.properties.digestSettings = digestSettings;
		
		if(digestFor.documentInfo && digestFor.documentInfo.digestDocAnnotId) {
			var postdata = {
					clientId : settings.clientId,
					digestDocAnnotId : digestFor.documentInfo.digestDocAnnotId,
					link : lnk.linkObj
			};
			updateLinkPromise = updateDigestDocAnnot(postdata);
		} else {
			updateLinkPromise = updateLink(lnk.linkObj,settings.clientId); 
		}
		
		if(updateLinkPromise) {
			updateLinkPromise.then(function(resp) {
				if(resp.status == 200 && resp.data.Status) {
					self.linkInfo = resp.data.Link;
					configDigestDataFromDigestLink();
					MessageService.showSuccessMessage("DIGEST_LINK_UPDATE");
					if(typeof cb == "function") {
						cb();
					}
				}
			});
		}
	}
	
	function copySuccess() {
		self.focusLink = false;
		APIUserMessages.info("Link copied to clipboard.");
		self.focusLink = true;
		$timeout(function() {
			$modalInstance.dismiss('cancel');
		}, 100);
	}
	
	function copyFail(err) {
		console.log(err);
	}
	
	function ShareOnSocialMedia(MediaChannel) {
		if(MediaChannel == "Twitter" || MediaChannel == "Facebook" || MediaChannel == "Whatsapp") {
			PostToSocialMedia(false,MediaChannel);
		}
		if(MediaChannel == "LinkedIn") {
			if(authUserPreferences.linkedInEnabled) {
				if(authUserPreferences.hasLinkedInAuth){
					PostToSocialMedia(true,MediaChannel);
				} else {
					confirmAndShareOnSocialMedia(MediaChannel);
				}
			} else {
				PostToSocialMedia(false,MediaChannel);
			}
		}
		
	}
			
	function PostToSocialMedia(showAPIPostFields,MediaChannel) {
		getShareCommentaryTextForSocialMedia(function(shareCommentaryTxt){
			var modalInstance = $modal.open({
				  animation: true,
				  template: require('../templates/post-to-social-media.html'),
				  controller: 'PostToSocialMediaModal',
				  controllerAs: 'ptsmc',
				  backdrop: 'static',
				  size : 'lg',
				  resolve: {
					  DigestLinkinfo :function() {
						  return self.linkInfo;
					  },
					  shareCommentaryTxt : function() {
						  return {"text":shareCommentaryTxt};
					  },
					  authUsrPreferences : function() {
						  var authPreferences = {};
			    		  if(authUserPreferences) {
			    			  authPreferences = angular.copy(authUserPreferences);
			    		  }
			    		  authPreferences["showAPIPostFields"] = showAPIPostFields;
						  return authPreferences;
					  },
					  MediaChannelInfo : function() {
						  return {"channel" : MediaChannel};
					  }
				  }
			});
		});
	}
	
	function authorize(authWindow) {
		var authResponse = new Promise((resolve, reject) => {
		      function authRespListener(event) {
		        if (event.data.type === 'linkedin_authorization_response') {
		        	resolve(event.data);
		        }
		        if (event.data.code === 'Digest Settings') {
			        resolve(event.data);
			    }
		        if (event.data.type === 'linkedin_authorization_canceled') {
		          reject(new Error('Linkedin Authorization window was closed'));
		        }
		        $window.removeEventListener('message', authRespListener);
		      }
		      $window.addEventListener('message', authRespListener);
		    });
		return authResponse;
	}
	
	function confirmAndShareOnSocialMedia(MediaChannel) {
		var confirmationMsgText = "Would like to authorize Numici to post messages in "+MediaChannel+"?";
		
		if(MediaChannel == "LinkedIn" && !_.isEmpty(authUserPreferences.linkedInAuthUsrConfMsgText)) {
			confirmationMsgText = authUserPreferences.linkedInAuthUsrConfMsgText;
		}
		var modalOptions = {
		          closeButtonText: 'AUTHORIZE LATER',
		          actionButtonText: 'AUTHORIZE NOW',
		          headerText: 'Confirm '+MediaChannel+' Authorization',
		          bodyText: confirmationMsgText
		      };

		confirm.showModal({}, modalOptions).then(function (result) {
			if(MediaChannel == "LinkedIn") {
				var authWindow = sharelinks.openJSPopupWindow("Linked Auth");
				authWindow.location = authUserPreferences.LinkedInAuthUrl;
				authorize(authWindow).then(respData => {
					if (respData) {
						if(respData.state == "Success") {
							authUserPreferences.hasLinkedInAuth = true;
							PostToSocialMedia(true,MediaChannel);
						}
					}
				});
			}
		},function() {
			if(MediaChannel == "LinkedIn") {
				PostToSocialMedia(false,MediaChannel);
			}
    	});
	}
	
	function checkAndCreateLink(cb) {
		if(!_.isEmpty(self.digestName) || self.digestFor == "DigestDocument") {
			if(!_.isEmpty(self.linkInfo.url)) {
				if(typeof cb == "function") {
					cb();
				}
			} else {
				var digestSettings = preProcessDigestDataForLink();
				//var lnk = processLinkInfo(digestSettings);
				var lnk = sharelinks.processLinkInfo(digestFor, settings.taskspaceId, settings.clientId, digestSettings);
				DeepLinkService.createLink(lnk).then(function(createLinkResp) {
					if(createLinkResp.status == 200 && createLinkResp.data.Status) {
						self.linkInfo = createLinkResp.data.Link;
						if(!_.isEmpty(self.linkInfo.properties)) {
							config = self.linkInfo.properties.digestSettings;
						} else if(!_.isEmpty(self.linkInfo.linkObj.properties)) {
							config = self.linkInfo.linkObj.properties.digestSettings;
						} else {
							config = _.isEmpty(settings.digestsettings) ? sharelinks.getDigestFilters() : settings.digestsettings;
						}
						if(typeof cb == "function") {
							cb();
						}
					}
				});
			}
		}
	}
	
	function getDigestContent(saveflag) {
		return $timeout(function() {
			var templateUrl = 'app/components/AnnotationDigest/save/annotaion-digest-template-new.html';
			var roottemplateUrl = 'app/components/AnnotationDigest/save/digest-root.tpl.html';
			
			sharelinks.enableBorder = "";
			sharelinks.enableBorder = angular.copy(self.enableBorderOption.value);
			var tmpl = '';
			var gptmpl = '';
			tmpl = '<annotaion-digest-template digest="vm.digestData"';
			gptmpl = '<annotaion-digest-group-template digest="vm.digestData"';
			
			if(!_.isEmpty(self.digestName)) {
				tmpl = tmpl+' digest-name="'+self.digestName+'"';
				gptmpl = gptmpl+' digest-name="'+self.digestName+'"';
			}
			
			if(!_.isEmpty(self.digestDescription)) {
				var digestDescription = angular.copy(self.digestDescription);
				digestDescription = digestDescription.replace(/(\r\n|\n|\r)/gm, "<br>");
				tmpl = tmpl+' description="'+digestDescription+'"';
				gptmpl = gptmpl+' description="'+digestDescription+'"';
			}
			
			tmpl = tmpl+' template-url="'+templateUrl+'" \
				task-space="taskspace"\
				title="true"\
				data-digest-meta-info-options="vm.digestMetaInfoOptions"\
				data-image-position="vm.imagePositionOption.value"\
				data-display-order="vm.displayOrderOption.value"\
				data-display-replies="vm.filterOptions.displayRepliesOption.value"\
				data-enable-border="vm.enableBorderOption.value"\
				data-group-by="vm.groupByOption.value"\
			    data-set-diges-min-max-width= "vm.setDigesMinMaxWidth()"\
				data-get-title-styles = "vm.getTitleStyles()"\
				data-alternate-image-styles = "vm.alternateImageStyles()"\
				data-set-rep-digest-styles = "vm.setRepDigestStyles(digest,vm.digestMetaInfoOptions.image)"\
				data-get-annotated-text = "vm.getAnnotatedText(annotation)"\
				data-format-created-date = "vm.formatCreatedDate(dateValue)"\
				data-format-comment="vm.formatComment(annotation,comment)"\
		    	comment-iconUrl = "vm.commentIconUrl"\
				></annotaion-digest-template>';
			
			gptmpl = gptmpl+' template-url="'+templateUrl+'" \
				task-space="taskspace"\
				data-digest-meta-info-options="vm.digestMetaInfoOptions"\
				data-image-position="vm.imagePositionOption.value"\
				data-display-order="vm.displayOrderOption.value"\
				data-display-replies="vm.filterOptions.displayRepliesOption.value"\
				data-enable-border="vm.enableBorderOption.value"\
				data-group-by="vm.groupByOption.value"\
				data-set-diges-min-max-width= "vm.setDigesMinMaxWidth()"\
				data-get-title-styles = "vm.getTitleStyles()"\
				data-alternate-image-styles = "vm.alternateImageStyles()"\
				data-set-rep-digest-styles = "vm.setRepDigestStyles(digest,vm.digestMetaInfoOptions.image)"\
				data-get-annotated-text = "vm.getAnnotatedText(annotation)"\
				data-format-created-date = "vm.formatCreatedDate(dateValue)"\
				data-format-comment="vm.formatComment(annotation,comment)"\
		    	comment-iconUrl = "vm.commentIconUrl"\
				></annotaion-digest-group-template>';
			
			var template = angular.element(tmpl);
			if(self.groupByOption.value == "tag" || self.groupByOption.value == "taghierarchical") {
				template = angular.element(gptmpl);
			} else {
			    template = angular.element(tmpl);
			}
			var linkFunction = $compile(template);
			var result = linkFunction($scope);
			$scope.$digest();
			if(saveflag) {
				var rootTemplate = $templateCache.get(roottemplateUrl);
				if(!_.isEmpty(rootTemplate)) {
					return rootTemplate.replace("<!--digestdata-->",template.html());
				}
				return template.html();
			} else {
				return template.html();
			}
			
		}, 0);
	}
	
	function getDigestContentAndSaveAsPdf(fileName) {
		getDigestContent(true).then(function(content) {
			sharelinks.SaveAsPdf(fileName,content,self.linkInfo);
		});
	}
		
	function processDataForPublish(result){
		var AnnotationDigestResp = result.AnnotationDigestResp;
		sharelinks.processDataForPublish(result,settings,self.digestMetaInfoOptions);
		var linkAnnotationDigestResp = AnnotationDigestResp.AnnotationDigest;
		trustedAnnotatedText = {};
		self.digestData = sharelinks.preProcessAnnotationDigestResp(linkAnnotationDigestResp,config,AnnotationDigestResp.dlId);
		if(!_.isEmpty(result.publishFor) && result.publishFor == "pdf") {
			getDigestContentAndSaveAsPdf(result.fileName);
		}
	}
	
	function cancelsaveAsSelectedType(SelectedType) {
		if(SelectedType == "pdf"){
			self.selectedPublishAsPdf = false;
			if(self.digestFor == 'AnnotationDigest') {
				self.selectedPublishAsLink = true;
			} else if(self.digestFor == 'DigestDocument') {
				self.showdocLink = true;
			}
		}
	}
	
	function saveAsSelectedType(SelectedType) {
		var publishedData = {"publishFor" : SelectedType};
		checkAndCreateLink(function() {
			sharelinks.createLinkAnnotationDigest({dLinkId : self.linkInfo.dlId},function(respData) {
				publishedData["AnnotationDigestResp"]= respData;
					if(publishedData.publishFor == "pdf"){
						if(!_.isEmpty(self.pdfName)) {
							publishedData["fileName"]= self.pdfName;
							processDataForPublish(publishedData);
						}
					}
			});
		});
	}
	
	function hideAllPublishOptions() {
		self.selectedPublishAsNote = false;
		self.selectedPublishAsPdf = false;
		self.selectedPublishAsLink = false;
		//self.showdocLink = false;
	}
	
	function publishAsSelectedType(SelectedType) {
		hideAllPublishOptions();
		if(SelectedType == "numicinote") {
			self.selectedPublishAsNote = true;
			self.noteName = angular.copy(self.digestName);
		}
		if(SelectedType == "pdf") {
			self.selectedPublishAsPdf = true;
			self.pdfName = angular.copy(self.digestName);
			if(self.pdfName.endsWith(".pdf")) {
				self.pdfName = self.pdfName.replace(".pdf","")
			}
		}
		if(SelectedType == "email") {
			self.selectedPublishAsLink = true;
			saveAsSelectedType(SelectedType);
		}
		if(SelectedType == "link") {
			checkAndCreateLink(function() {
				self.selectedPublishAsLink = true;
			});
		}
	}
	
	function checkAndPublishAsSelectedType(SelectedType) {
		publishAsSelectedType(SelectedType);
	}
	
	self.copyToClipboard = function (event) {
	    var $container = angular.element(event.currentTarget).parent();
	    var shareLinkInput = $container.find('input')[0];

	    try {
	      shareLinkInput.select();

	      // In some browsers, execCommand() returns false if it fails,
	      // in others, it may throw an exception instead.
	      if (!document.execCommand('copy')) {
	        throw new Error('Copying link failed');
	      }

	      self.copyToClipboardMessage = 'Link copied to clipboard!';
	    } catch (ex) {
	      self.copyToClipboardMessage = 'Select and copy to share.';
	    } finally {
	      setTimeout(function () {
	        self.copyToClipboardMessage = null;
	        $scope.$digest();
	      }, 1000);
	    }
	};
  
	function init() {
		configDigestDataFromDigestLink();
		if(!_.isEmpty(config.groupBy)) {
			var gpby = _.findWhere(self.groupByOptionList,{value: config.groupBy})
			if(gpby) {
				self.groupByOption = gpby;
			}
		}
	}
	
	init();
}

module.exports = ShareLinkModal;