'use strict';

/**
 * @description Displays a combined privacy/selection post button to post
 *              a new annotation
 */
// @ngInject
module.exports = {
  controller: function (addSection) {
    var self = this;
    self.createSection = function() {
		if(self.group) {
			addSection.add(self.group).then(function(section) {
				if(self.onNewSection) {
					self.onNewSection({section:section,group: self.group});
				}
			},function(){
				
			});
		}
	}
  },
  controllerAs: 'vm',
  bindings: {
    group: '<',
    auth:'<',
    label:'@',
    onNewSection:'&',
  },
  template: require('../templates/add-section-btn.html'),
};
