'use strict';

var sessionUtil = require('../util/session-util');
var uiConstants = require('../ui-constants');

module.exports = {
  controllerAs: 'vm',
  //@ngInject
  controller: function ($scope,$element, store, features, session, settings,groups) {
	var self = this;
    this.TAB_ANNOTATIONS = uiConstants.TAB_ANNOTATIONS;
    this.TAB_NOTES = uiConstants.TAB_NOTES;
    this.TAB_ORPHANS = uiConstants.TAB_ORPHANS;
    this.WEB_CLIP = uiConstants.WEB_CLIP;
    this.TAB_PAGEINFO = uiConstants.TAB_PAGEINFO;

    this.isThemeClean = settings.theme === 'clean';
    this.isEmbedded =  settings.appType === "embedded";
    this.isWebClipEnabled = false;
    this.enableExperimentalNewNoteButton = settings.enableExperimentalNewNoteButton;
    
    $scope.$on("showPageInfo", function () {
      self.selectTab(uiConstants.TAB_PAGEINFO);
    });

    $scope.$watch(() => ([groups.focusedId()]), ([currentGroupId], [prevGroupId]) => {
      	if(currentGroupId !== prevGroupId ) {
	        self.isWebClipEnabled = false;
      		var group = groups.focused();
      		if(group) {
      			if(group.fromPublicLink) {
      				self.isWebClipEnabled = false;
          		} else if(self.auth && self.auth.preferences) {
	                var enableWebClip = self.auth.preferences.enableWebClip;
	                self.isWebClipEnabled = enableWebClip && ( group.permissions && group.permissions.edit);
				}
      		}
      	}
    }, true);

    this.selectTab = function (type) {
      store.clearSelectedAnnotations();
      store.selectTab(type);
    };

    this.showOrphanedAnnotHelp = function () {
    	if(self.auth && self.auth.preferences) {
    		this.OrphanedAnnotHelpTitle = "Orphaned Annotations";
    		this.OrphanedAnnotHelpDesc = self.auth.preferences.OrphanedAnnotations;
        }
    };
      
    this.showAnnotationsUnavailableMessage = function () {
      return this.selectedTab === this.TAB_ANNOTATIONS &&
        this.totalAnnotations === 0 &&
        !this.isWaitingToAnchorAnnotations;
    };

    this.showNotesUnavailableMessage = function () {
      return this.selectedTab === this.TAB_NOTES &&
        this.totalNotes === 0;
    };

    this.showSidebarTutorial = function () {
      return sessionUtil.shouldShowSidebarTutorial(session.state);
    };
  },
  bindings: {
    isLoading: '<',
    isWaitingToAnchorAnnotations: '<',
    selectedTab: '<',
    totalAnnotations: '<',
    totalNotes: '<',
    totalOrphans: '<',
    auth: '<',
    OrphanedAnnotHelpTitle: '<',
    OrphanedAnnotHelpDesc: '<',
  },
  template: require('../templates/selection-tabs.html'),
};
