/**
 * @ngdoc service
 * @name  userstate
 *
 * @description Provides access to the user ui settings
 */
'use strict';

var kyes = require('../keys');

function updateGroupmodel(state) {
  return Object.assign({}, {
    // Apply changes from the draft
   id: state.id || null,
   stateKey: kyes.GROUPS_UI_KEY,
   stateValue: state.groupId,
  });
}
// @ngInject
function userState(api) {
	function saveState(params) {
		return api.profile.saveUserState({},params);
	}
	
	function getState(params) {
		return api.profile.getUserState({},params);
	}
    
    function getGroupState(params) {
	    var postdata = updateGroupmodel(params || {});
		return api.profile.getUserState({},postdata);
	}
	
	function saveGroupState(params) {
		var postdata = updateGroupmodel(params);
		return api.profile.saveUserState({},postdata);
	}
	
	return {
		saveState : saveState,
		getState: getState,
		getGroupState: getGroupState,
		saveGroupState: saveGroupState,
	};
}

module.exports = userState;
