'use strict';

/**
 * A service for displaying "Add New section to the taskspace".
 */

// @ngInject
function AddSection($modal) {
	
	var modalOptions = {
	    close: 'CANCEL',
	    ok: 'CREATE',
	    headerText: 'Add New Section to Taskspace'
	};
	this.modalInstance = null;
	this.add = function (group) {
		  if(this.modalInstance) {
			this.modalInstance.dismiss("cancel");
			this.modalInstance = null;
		  }

	      this.modalInstance = $modal.open({
				  animation: true,
				  template: require('../templates/add-new-section.html'),
				  controller: 'AddSectionModalController',
				  controllerAs: 'vm',
		          windowClass: 'create_section',
				  backdropClass:'create_section', 
				  backdrop: 'static',
				  size : 'lg',
				  resolve: {
					  toGroup :function() {
						  return group;
					  },
			 		  modalOptions: function() {
						var mdlOptions = angular.copy(modalOptions);
						if(group) {
							mdlOptions.headerText = 'Add New Section to "'+group.name +'"';
						} 
						return mdlOptions;
					}
				  }
			});
			return this.modalInstance.result;
	};
	
}

module.exports = AddSection;