'use strict';


//@ngInject
function CookieErrorController($scope, api, groups, settings,$window) {
  var self = this;
  
 
  self.tab = 1;

  self.setTab = function(newTab){
	  self.tab = newTab;
  };

  self.isSet = function(tabNum){
    return self.tab === tabNum;
  };
  
  self.getCookieErrorMessage = function() {
	let domain =  settings.authDomain ? new URL(settings.authDomain).hostname : "";
	return "Your browser seems to have disabled third-party cookies. Make sure \""+domain+"\" cookies are enabled.Refresh the page once enabled";  
  };
}

module.exports = {
  controller: CookieErrorController,
  controllerAs: 'vm',
  bindings: {
    onClose: '&',
    auth: '=',
  },
  template: require('../templates/cookie-error-dialog.html'),
};
