'use strict';

var { isThirdPartyUser } = require('../util/account-id');
var isThirdPartyService = require('../util/is-third-party-service');
var serviceConfig = require('../service-config');
const memoize = require('../util/memoize');
const groupOrganizations = memoize(require('../util/group-organizations'));

// @ngInject
function GroupListController($scope,$window, analytics, groups, settings, serviceUrl, $rootScope, $timeout, frameSync,flash,userPreference) {
	var self = this;
	this.groups = groups;
	this.isOpen = groups.openGroupdropDown;
	this.tsSearchTxt = "";

	$scope.$on('requestGroupChange', function() {
		openGroupdropDown();
	});
	
	$scope.$on('windowFocus', function() {
		self.close();
	});
	
	function openGroupdropDown() {
		$timeout(function() {
			self.isOpen = true;
			$rootScope.$broadcast('FocusFindTaskSpace', true);
		}, 0);
	}

	function init() {
		if (groups.openGroupdropDown) {
			frameSync.showSidebar();
			openGroupdropDown();
		}
	}

    this.close = function() {
		self.isOpen = false;
	};
	
	let docSections = {};
	this.getSection = function(group) {
		if (group) {
			if (docSections[group.id]) {
				return docSections[group.id];
			} else if (group.webresource) {
				return groups.getDocSection(group);
			} else {
				return groups.getfocusedSection(group.id);
			}
		}
		return null;
	};

	this.disableSection = function(group) {
		if (group.webresource || groups.getDocSection(group)) {
			return true;
		}
		return false;
	}

	this.showSections = function(group) {
		if(groups.isEditable(group)) {
			if (self.tsSearchTxt && (self.hasSections(group))) {
				return true;
			}
			if (groups.focused()) {
				return groups.focused().id == group.id;
			}
		}
		return false;
	};

	this.hasSections = function(group) {
		return group.sections && group.sections.length;
	};

	this.onNewSection = function(data) {
		if(data.group) {
			this.focusSection({groupId: data.group.id,section:data.section});
		} else if (data.section && groups.focused()) {
			groups.focusSection(groups.focused().id, data.section);
		}
	};

	this.isSelectable = function(group) {
		return groups.isSelectable(group);
	};

	this.toggled = function(open) {
		this.tsSearchTxt = "";
		$rootScope.$broadcast('FocusFindTaskSpace', true);
	};

	this.createNewGroup = function() {
		$window.open(serviceUrl('groups.new'), '_blank');
	};

	this.focusedIcon = function() {
		const focusedGroup = this.groups.focused();
		return focusedGroup && (
			focusedGroup.organization.logo || this.thirdPartyGroupIcon
		);
	};

	this.focusedIconClass = function() {
		const focusedGroup = this.groups.focused();
		return (focusedGroup && focusedGroup.type === 'private') ? 'group' : 'public';
	};

	this.isThirdPartyUser = function() {
		return isThirdPartyUser(this.auth.userid, settings.authDomain);
	};

	this.leaveGroup = function(groupId) {
		var groupName = groups.get(groupId).name;
		var message = 'Are you sure you want to leave the group "' +
			groupName + '"?';
		if ($window.confirm(message)) {
			analytics.track(analytics.events.GROUP_LEAVE);
			groups.leave(groupId);
		}
	};

	this.orgName = function(groupId) {
		const group = this.groups.get(groupId);
		return group && group.organization && group.organization.name;
	};

	this.groupOrganizations = function() {
		return groupOrganizations(this.groups.all());
	};

	this.viewGroupActivity = function() {
		analytics.track(analytics.events.GROUP_VIEW_ACTIVITY);
	};

	this.focusGroup = function(groupId) {
		self.close();
		analytics.track(analytics.events.GROUP_SWITCH);
		groups.focus(groupId);
		/*let group = groups.focused(groupId);
		let section = this.getSection(group);
		groups.focusSection(groupId, section);*/
	};

	this.focusSection = function(data) {
		groups.focusSection(data.groupId, data.section);
		this.focusGroup(data.groupId);
	};

	/**
	 * Show the share link for the group if it is not a third-party group
	 * AND if the URL needed is present in the group object. We should be able
	 * to simplify this once the API is adjusted only to return the link
	 * when applicable.
	 */
	this.shouldShowActivityLink = function(groupId) {
		const group = groups.get(groupId);
		return group.links && group.links.html && !this.isThirdPartyService;
	};

	var svc = serviceConfig(settings);
	if (svc && svc.icon) {
		this.thirdPartyGroupIcon = svc.icon;
	}

	this.isThirdPartyService = isThirdPartyService(settings);
	this.allowCreateTaskSpace = userPreference.canCreateTaskspace;
	this.allowCreateSection = userPreference.canCreateSection;

	/*this.allowCreateTaskSpace = function() {
		return (this.auth.status === 'logged-in'
			&& !this.isThirdPartyUser()
			&& !this.auth.isPublic
			&& this.auth.preferences
			&& this.auth.preferences.createTaskspace);
	};*/

	this.selectTaskspace = function() {
		return (this.auth.status === 'logged-in' && !this.groups.focused());
	};

	this.newTaskspace = function() {
		if (self.auth.status !== 'logged-out') {
			groups.createTaskspace(self.tsSearchTxt);
		} else {
			flash.error("You must login to create the Taskspace",{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
		}
	};

}

module.exports = {
	controller: GroupListController,
	controllerAs: 'vm',
	bindings: {
		auth: '<',
		onNewGroup: '&',
		newGroup: '@',
	},
	template: require('../templates/group-list.html'),
};


