'use strict';

// @ngInject
function DocTagEditorController(tags, api, confirm) {
	var self = this;
	this.onTagAdding = function($tag) {
		var status = true;
		var txt = $tag.text;
		if (txt.indexOf(":") > -1) {
			txt = txt.split(":")[0];
		}

		if (!txt) {
			status = false;
			return status;
		}

		this.tagList.forEach(function(item) {
			var itemText = item.text;
			if (itemText.indexOf(":") > -1) {
				itemText = itemText.split(":")[0];
				if (!itemText) {
					status = false;
					return status;
				}
			}

			if (itemText && txt && itemText.trim().toLowerCase() === txt.trim().toLowerCase()) {
				status = false;
				return status;
			}
		});
        if(status) {
			$tag.isNew = true;
		}
		return status;
	};

	this.onTagRemoving = function($tag) {
		let defer = api.createPromise();
			var msg = 'Are you sure you want to remove the tag "' + $tag.text + '"?';
			var modalOptions = {
				closeButtonText: 'Cancel',
				actionButtonText: 'Ok',
				headerText: 'Delete tag?',
				bodyText: msg
			};

			confirm.showModal({}, modalOptions).then(function() {
				if (!$tag.isNew && $tag.tagId) {
					self.onUntag({ tag: $tag });
					defer.resolve(true);
				} else {
					defer.resolve(true);
				}
			}, function() {
				defer.reject(false);
			});
			return defer.promise;
		
	};

	this.onTagsChanged = function() {
		tags.store(this.tagList);
		var newTags = this.tagList.map(function(item) {
			if (item.text.indexOf(":") > -1) {
				var tagInfo = item.text.split(":");
				if (tagInfo[1]) {
					return { "TagName": tagInfo[0].trim(), "Value": tagInfo[1].trim() ,"tagId": item.tagId, "isNew": item.isNew};
				}
			}
			return { "TagName": item.text ,"Value": null, "tagId": item.tagId,"isNew": item.isNew};
		});
		this.onTagsEdited({ tags: newTags });
	};

	this.autocomplete = function(query) {
		return api.tags.searchResourceTags({}, { "searchKey": query }).then(function(resp) {
			if (resp && resp.length > 0) {
				return resp.map(function(tag) {
					if (tag.Value) {
						return { text: tag.TagName + ":" + tag.Value ,tagId: tag.TagId , isNew: tag.isNew};
					}
					return { text: tag.TagName, tagId: tag.TagId , isNew: tag.isNew};
				});
			} else {
				return [{ text: query, tagId: null , isNew: true}];
			}
		});
	};

	this.$onChanges = function(changes) {
		if (changes.tags && changes.tags.currentValue) {
			this.tagList = changes.tags.currentValue.map(function(tag) {
				if (tag.Value) {
					return { text: tag.TagName + ":" + tag.Value ,tagId: tag.TagId , isNew: tag.isNew};
				}
				return { text: tag.TagName, tagId: tag.TagId , isNew: tag.isNew};
			});
		}
	};
}

module.exports = {
	controller: DocTagEditorController,
	controllerAs: 'vm',
	bindings: {
		tags: '<',
		onTagsEdited: '&',
		onUntag: '&',
	},
	template: require('../templates/doc-tag-editor.html'),
};
