'use strict';
var events = require('../events');
// @ngInject
function ClipperControlesController($rootScope,$scope, frameSync, $location, $timeout,
		juice, $document,flash,api,groups,confirm) {
	var self = this;
	this.editClip = false;
	this.webClip = null;
	this.clipname = null;
	
	$scope.$on("webClipChanged", function (event, article) {
		self.getClip();
	});
	
	$scope.$on('$destroy', function() {
		frameSync.closeClipper();
	});
	
	$scope.$on(events.GROUP_FOCUSED, function (event, focusedGroupId) {
		frameSync.closeClipper();
	    init();
    });
	
	this.slider = {
			value: 0.6,
			options: {
			    floor: 0.1,
			    ceil: 0.75,
			    step:0.05,
			    precision: 2,
			    onEnd: function() {
		            self.getClip();
		        }
			}
	};
	
	this.isSavedClip = function() {
		if(self.webClip && self.webClip.clipUrl) {
			return true;
		}
		return false;
	};
	
	function saveClip() {
		try{
			var doc = juice.inlineCss(self.article);
			var group = groups.focused();
			var webclip = {
				 "group": group.id,
				 "tsClientId": group.clientId,
				 "uri" : self.article.url,
				 "content": doc,
				 "title" : self.clipname,
				 "type" : "WebClip",
				 "objId" : group ? group.objId : null,
			     "objClientId" : group ? group.objClientId : null,
			     "score" : self.slider.value,
			     "log" : self.article.log
			};
			
			if(self.webClip) {
				webclip.clipId = self.webClip.objectId;
				webclip.clipClientId = self.webClip.clientId;
			}
			
			api.webclip.create({}, webclip).then(function(resp){
				flash.success(' Clip Successful');
				self.close();
				self.webClip = resp.webClip;
				group.webClip = resp.webClip;
	    	}).catch(function (err) {
	    		flash.error(err+' Saving Web Clip Failed',{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
	        });
		} catch(e) {
			
		}
	}
	
	
	this.saveClip = function() {
		if (self.auth && self.auth.status === 'logged-in') {
			var group = groups.focused();
			if(group) {
				if(self.webClip) {
					if(self.webClip.annotationCount) {
					  var msg = 'Previous Annotaions will be deleted ,Are you sure you want to save WebClip?';
				      var modalOptions = {
				          closeButtonText: 'Cancel',
				          actionButtonText: 'Ok',
				          headerText: 'Retake WebClip?',
				          bodyText: msg
				      };
				      confirm.showModal({}, modalOptions).then(function (result) {
				    	  saveClip();
				      },
				      function() {
				    	  
				      });
					} else {
						saveClip();
					}
				} else {
					saveClip();
				}
			} else {
				flash.error('Please Select Group',{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
			}
		}else {
			flash.error('Login to Save clip',{extendedTimeOut: 0,tapToDismiss: false,timeOut: 0,closeButton: true,});
		}
	};

	this.getClip = function() {
		frameSync.getClip({closeScore: self.slider.value});
	};
	
	this.close = function() {
		frameSync.closeClipper();
		self.editClip = false;
	}
	
	this.retakeClip = function() {
		frameSync.showClipper();
		self.editClip = true;
	};
	
	$scope.$watch(angular.bind(this, function () {
		  return this.article;
	}), function (article) {
		if(self.webClip) {
			self.clipname = angular.copy(self.webClip.name);
		} else if(article) {
			self.clipname = angular.copy(article.title); 
		}
	}, true);
	  
	
	
	function init() {
		frameSync.showClipper();
		$timeout(function(){
			var group = groups.focused();
			if(group) {
				self.webClip = group.webClip;
				if(self.webClip) {
					self.clipname = angular.copy(self.webClip.name);
					self.close();
				} else if(self.article) {
					self.clipname = angular.copy(self.article.title);
					self.retakeClip(); 
				}
			}
		},0);
	}
	
	init();
}

module.exports = {
	controller : ClipperControlesController,
	controllerAs : 'vm',
	bindings : {
		auth : '<',
		article : '=',
	},
	template : require('../templates/clipper-controles.html'),
};
