'use strict';
var events = require('../events');
// @ngInject
function SectionListController($scope,groups) {
  'ngInject';
  var self= this;
  self.groups = groups; 
  self.group = null;
  self.focusSection = function (section) {
	if(self.group) {
		groups.focusSection(self.group.id,section);
	}
  };

  this.allowCreateSection = function() {
	  return (this.auth.status === 'logged-in' 
		  && !this.isThirdPartyUser() 
		  && !this.auth.isPublic);
  };

  $scope.$watch(() => ([groups.focusedId()]), ([currentGroupId], [prevGroupId]) => {
	if (currentGroupId !== prevGroupId) {
		self.group = groups.focused();
	}
  }, true);

  $scope.$on(events.GROUP_UPDATED,function(event,groupId){
	if(self.group && self.group.id == groupId) {
		self.group = groups.get(groupId);
	}
  });
}

module.exports = {
  controller: SectionListController,
  controllerAs: 'vm',
  bindings: {
    auth: '<'
  },
  template: require('../templates/section-list.html'),
};


