'use strict';

var events = require('../events');


//@ngInject
function extCommunicator(sessionUUID,settings,$q,$rootScope,timelog) {
	
	var self = this;
	var port = null;
	
	self.pendingRequests = 0;

	if(chrome.runtime.connect) {
		port = chrome.runtime.connect({name: "numici_"+sessionUUID});
		port.onMessage.addListener(function(request) {
			  if (request && request.type === 'page-rendered') {
	            $rootScope.$broadcast("urlChanged");
			  }
		});
	}
	
	/*if(chrome.runtime.onMessage) {
		chrome.runtime.onMessage.addListener(function(request) {
		  if (request && request.type === 'page-rendered') {
            $rootScope.$broadcast("urlChanged");
		  }
		});
	}*/
	
	function processMessage(message) {
		
		if(message) { 
			
			if(!message.headers) {
				message.headers = {};
			}
			
			message.headers['X-Client-Id'] = sessionUUID;
  			message.headers['X-Client-Version'] = settings.release;
  			message.headers['X-Client-Type'] = settings.appType;
	
		    if(typeof message.paramSerializer == "function" && Object.keys(message.params || {}).length != 0) {
				message.url +="?"+message.paramSerializer( message.params);
				console.log(message.url);
			}
			
			if(message.data && Object.keys(message.data || {}).length != 0 && message.contentType == "application/json") {
				message.data = JSON.stringify(message.data);
			}

			
			return {
				action: message.action,
				requestTimestamp: new Date().getTime(),
				request: {
					 data: message.data ? message.data : null,
					 headers: message.headers,
					 url: message.url,
   					 type: message.method,
 	                 contentType: message.contentType,
				}
			};
		}
		return null;
		
	}
	
	
	function sendMessage(message) {
		let defer = $q.defer();
		self.pendingRequests++;
		let postmessage = processMessage(message);
		try{
			if(chrome.runtime.sendMessage) {
				if(postmessage) {
					chrome.runtime.sendMessage(postmessage,function(response) {
						if(postmessage.action == "apicall" && response.status === 401) {
							$rootScope.$broadcast(events.OAUTH_TOKENS_ERROR);
							defer.reject(response);
				        }
                        defer.resolve(response);
					});
				} else {
					defer.reject();
				}
			} else {
				defer.reject();
			}
		} catch (e) {
			defer.reject();
		}
		defer.promise.finally(function() {
			if(self.pendingRequests > 0){
				self.pendingRequests--;
			}
			
			postmessage.responseTimestamp = new Date().getTime();
    	    var time = postmessage.responseTimestamp - postmessage.requestTimestamp;
    	    time = (time/1000);
    	    if(time >= 0 && postmessage.action == "apicall") {
	    		var timeLog = {
	    			   'timeStamp': postmessage.requestTimestamp,
	    			   'requestedUrl' : postmessage.request.url,
	    			   'responseTime' : time
	    		};
	    		timelog.setTimeLogInfo(timeLog);
    	    }
		});
		return defer.promise;
	}
	
	function getPort() {
		return port;
	}
	
	function getPendingRequestCount() {
		return self.pendingRequests;
	};
	
	return {
		sendmessage : sendMessage,
		getPort: getPort,
		getPendingRequestCount: getPendingRequestCount
	};
}

module.exports = extCommunicator;
