'use strict';
var events = require('../events');

/* @ngInject */
function AddToTaskspaceModalController($scope, groups, $modalInstance,auth,userPreference) {
	'ngInject';
	var self = this;
	self.auth = auth;
	self.groups = groups;
	
	self.modalOptions = {
	    cancel: 'Cancel',
	    ok: 'Ok',
        addSection: "Add Section",
	    headerText: 'Add Web Page to Taskspace'
	};
	
	
	$scope.$watch(angular.bind(self, function () {
	  return self.group;
	}), function (newGroup, oldGroup) {
	    self.group = newGroup;
		self.section = self.group ? self.groups.getfocusedSection(newGroup.id) : {};
	});
	
	
	$scope.$on(events.GROUP_UPDATED,function(event,groupId){
		if(self.group && self.group.id == groupId) {
			self.group = groups.get(groupId);
		}
	});
	
	self.cancel = function() {
		$modalInstance.dismiss('cancel');
	};
	 
    self.ok = function() {
		let data = update();
		$modalInstance.close(data);
	};
	
	self.allowCreateSection = userPreference.canCreateSection;
	
	self.hasSections = function() {
		if(self.group) {
			return self.group.sections && self.group.sections.length > 0;
		}
		return false;
	};
	
	function update() {
		return Object.assign({},{
			group: self.group,
			section:self.section
		});
	}
	
	self.onNewSection = function(data) {
		self.section = data.section;
		//groups.addSection(self.group.id,data.section);
	}
	
	function init() {
		self.group = groups.focused();
		self.section = self.group ? self.groups.getfocusedSection(self.group.id) : null;
	}
	
	init();
}

module.exports =AddToTaskspaceModalController;
