'use strict';

const util = require('../util');

function init() {
  return {
    /**
     * List of groups.
     * @type {Group[]}
     */
    groups: [],

    /**
     * ID of currently selected group.
     * @type {string|null}
     */
    focusedGroupId: null,
    focusedSection: {},
  };
}

const update = {
  FOCUS_GROUP(state, action) {
    const group = state.groups.find(g => g.id === action.id);
    return { focusedGroupId: group ? action.id : null };
  },

  FOCUS_SECTION(state, action) {
	var groupId = action.groupId;
	var section = action.section;
    var sections = Object.assign({}, state.focusedSection);
    sections[groupId] = action.section;
    var groups = state.groups.map(function (group) {
	  if (group.id !== action.groupId) {
		  return group;
	  }
	  return Object.assign({}, group, { focusSection: section ? section.id : null });
	});
    return { groups: groups, focusedSection:sections};
  },

  ADD_SECTION(state, action) {
	var groupId = action.id;
    var sections = Object.assign({}, state.focusedSection);
    sections[groupId] = action.section;
	var groups = state.groups.map(function (group) {
	  if (group.id !== action.id) {
		  return group;
	  }
      let gpSections = group.sections || [];
      gpSections.push(action.section);
	  return Object.assign({}, group, { sections: gpSections,focusSection: action.section ? action.section.id : null });
	});
	return { groups: groups, focusedSection:sections};
  },

  LOAD_GROUPS(state, action) {
    const groups = action.groups;
    let focusedGroupId = state.focusedGroupId;

    // Reset focused group if not in the new set of groups.
    /*  if (state.focusedGroupId === null || !groups.find(g => g.id === state.focusedGroupId)) {
      if(action.lastselected && groups.find(g => g.id === action.lastselected)) {
		focusedGroupId = action.lastselected;
	  }else if (groups.length > 0) {
        focusedGroupId = groups[0].id;
      } else {
        focusedGroupId = null;
      }
    }*/

    return {
      focusedGroupId,
      groups: action.groups,
    };
  },
  UPDATE_COUNT(state, action) {
	  var groups = state.groups.map(function (group) {
		  if (group.id !== action.id) {
			  return group;
		  }
		  return Object.assign({}, group, { annotationCount: action.count });
	  });
	  return { groups: groups};
  },
  UPDATE_GROUP(state, action) {
	  var groups = state.groups.map(function (group) {
		  if (group.id !== action.id) {
			  return group;
		  }
		  var count = group.annotationCount+action.count;
		  if(count >= 0 ) {
			  return Object.assign({}, group, { annotationCount: count,objId: action.objId,objClientId: action.objClientId,objOwner: action.objOwner,webresource: action.webresource});
		  } else {
			  return group;
		  }
	  });
	  return { groups: groups};
  },
  REPLACE_GROUP(state, action) {
	  var groups = state.groups.map(function (group) {
		  if (group.id !== action.id) {
			  return group;
		  }
		 return Object.assign({}, group, action.group);
	  });
	  return { groups: groups};
  }
};

const actions = util.actionTypes(update);

/**
 * Set the current focused group.
 *
 * @param {string} id
 */
function focusGroup(id) {
  //console.log("focusGroup from store "+ id);
  return {
    type: actions.FOCUS_GROUP,
    id,
  };
}


/**
 * Set the current focused section in a group.
 *
 * @param {string} groupId
   @param {Object} section
 */
function focusSection(groupId,section) {
  //console.log("focusGroup from store "+ id);
  return {
    type: actions.FOCUS_SECTION,
    groupId,
    section,
  };
}

/**
 * Set the current focused section in a group.
 *
 * @param {string} id
   @param {Object} section
 */
function addSection(id,section) {
  //console.log("focusGroup from store "+ id);
  return {
    type: actions.ADD_SECTION,
    id,
    section,
  };
}

/**
 * Set the  group count.
 *
 * @param {string} id
 */
function updateCount(id,count) {
  return {
    type: actions.UPDATE_COUNT,
    id,
    count,
  };
}

/**
 * Increment group count.
 *
 * @param {string} id
 */
function updateGroup(id,count,webresource) {
  return {
    type: actions.UPDATE_GROUP,
    id: id,
    count: count,
    objId: webresource ? webresource.objectId : null,
    objClientId: webresource ? webresource.clientId : null,
    objOwner: webresource ? webresource.objOwner : null,
    webresource: webresource ? webresource : null,
  };
}

function replaceGroup(id,group) {
  return {
    type: actions.REPLACE_GROUP,
    id: id,
    group: group,
  };
}

/**
 * Update the set of loaded groups.
 *
 * @param {Group[]} groups
 */
function loadGroups(groups,lastselected) {
  return {
    type: actions.LOAD_GROUPS,
    groups,
    lastselected,
  };
}

/**
 * Return the currently focused group.
 *
 * @return {Group|null}
 */
function focusedGroup(state) {
  if (!state.focusedGroupId) {
    return null;
  }
  return getGroup(state, state.focusedGroupId);
}


/**
 * Return the currently focused group.
 *
 * @return {Group|null}
 */
function getfocusedSection(state,groupId) {
  let section = null;	
  let group = getGroup(state,groupId);
  if(group && group.sections && group.focusSection) {
	section = group.sections.find(function (sec) {
		return sec.id == group.focusSection;
	});
  }
  /*if (state.focusedSection[groupId]) {
    return state.focusedSection[groupId];
  }*/
  return section;
}


/**
 * Return the current focused group ID or `null`.
 *
 * @return {string|null}
 */
function focusedGroupId(state) {
  return state.focusedGroupId;
}

/**
 * Return the list of all groups.
 *
 * @return {Group[]}
 */
function allGroups(state) {
  return state.groups;
}

/**
 * Return the group with the given ID.
 *
 * @return {Group|undefined}
 */
function getGroup(state, id) {
  return state.groups.find(g => g.id === id);
}

module.exports = {
  init,
  update,
  actions: {
    focusGroup,
    focusSection,
    addSection,
    loadGroups,
    updateCount,
    updateGroup,
    replaceGroup
  },
  selectors: {
    allGroups,
    getGroup,
    focusedGroup,
    focusedGroupId,
    getfocusedSection
  },
};
