'use strict';
var renderMarkdown = require('../render-markdown');
module.exports = ['$sce','$filter','$sanitize', function($sce,$filter,$sanitize) {
	return {
	      replace: true,
	      restrict: 'E',
	      link: function (scope, element, attrs) {
	    	  scope.$watch('comment', function (newValue) {
	              var showdownHTML;
	              if(scope.annotation && (scope.annotation.webAnnotation || scope.annotation.documentType == "WebResource")) {
	            	  //showdownHTML =  markdown.makeHtml(newValue || '');
	            	  showdownHTML =  renderMarkdown(newValue || '', $sanitize);
	  			  } else {
	  				  showdownHTML = $filter('linky')(newValue,'_blank');
	  			  }
	              
	              if(showdownHTML) {
	            	  scope.trustedHtml = $sce.trustAsHtml(showdownHTML);
	              }
	           });
        },
	      scope: {
	        comment: '=',
	        annotation:'='
	      },
	      template: '<div ng-bind-html="trustedHtml "></div>'
	};
}];