'use strict';

/**
 * A service for displaying "confirm"  messages.
 */

// @ngInject
function ConfirmAnnotationAction($modal) {
	var modalDefaults = {
		backdrop: true,
		keyboard: true,
		modalFade: true,
		template: require('../templates/confirm-annotation-action.html')
	};

	var modalOptions = {
		selectedGroup: {},
		closeButtonText: 'Cancel',
		moveButtonText: 'Submit',
		headerText: 'Confirm Action on Annotations'
	};

	this.showModal = function(customModalDefaults, customModalOptions) {
		if (!customModalDefaults) customModalDefaults = {};
		customModalDefaults.backdrop = 'static';
		return this.show(customModalDefaults, customModalOptions);
	};

	this.show = function(customModalDefaults, customModalOptions) {
		//Create temp objects to work with since we're in a singleton service
		var tempModalDefaults = {};
		var tempModalOptions = {};

		//Map angular-ui modal custom defaults to modal defaults defined in service
		angular.extend(tempModalDefaults, modalDefaults, customModalDefaults);

		//Map modal.html $scope custom properties to defaults defined in service
		angular.extend(tempModalOptions, modalOptions, customModalOptions);

		if (!tempModalDefaults.controller) {
			tempModalDefaults.controller = function($scope, $modalInstance,_,confirm) {
				$scope.moveWithAnnotations = { "option": "" };

				$scope.disableConfirm = disableConfirm;
				$scope.ok = ok;
				$scope.cancel = cancel;
				
				function disableConfirm() {
					if (!_.isEmpty($scope.moveWithAnnotations.option)) {
						return false;
					}
					return true;
				}

				function confirmDeleteAnnotations() {
					var text = "<p>Are you sure you want to deleted the annotations? (annotations would be deleted permanently)</p>\
				                <p>Later You will not be able to retrieve deleted Annotations.</p>";
					var modalOptions = {
						closeButtonText: 'Cancel',
						actionButtonText: 'Ok',
						headerText: 'Delete Annotations ?',
						bodyText: text
					};
					return confirm.showModal({}, modalOptions);
				}

				function ok() {
					if ($scope.moveWithAnnotations.option === "delete") {
						confirmDeleteAnnotations().then(function() {
							$modalInstance.close($scope.moveWithAnnotations.option);
						}, function() {
							cancel();
						});
					} else {
						$modalInstance.close($scope.moveWithAnnotations.option);
					}
				}

				function cancel() {
					$modalInstance.dismiss('cancel');
				}
			}
			tempModalDefaults.controller.$inject = ["$scope", "$modalInstance","_","confirm"];
		}

		return $modal.open(tempModalDefaults).result;
	};
}

module.exports = ConfirmAnnotationAction;