'use strict';

/**
 * A service for displaying "Add New section to the taskspace".
 */

// @ngInject
function AddToTaskspace($modal) {
	this.modalInstance = null;
	this.add = function (auth) {
		  if(this.modalInstance) {
			this.modalInstance.dismiss("cancel");
			this.modalInstance = null;
		  }

	      this.modalInstance = $modal.open({
				  animation: true,
				  template: require('../templates/add-to-taskspace.html'),
				  controller: 'AddToTaskspaceModal',
				  controllerAs: 'vm',
				  backdrop: 'static',
				  size : 'lg',
		          resolve: {
					auth: function() {
						return auth;
					}
				  }
			});
			return this.modalInstance.result;
	};
	
}

module.exports = AddToTaskspace;