'use strict';

var isThirdPartyService = require('../util/is-third-party-service');

module.exports = {
  controllerAs: 'vm',
  //@ngInject
  controller: function ($scope,settings,groups,addSection) {
	
	$scope.move = false;
	$scope.share = false;
	$scope.groupinfo = false;
	
    if (settings.theme && settings.theme === 'clean') {
      this.isThemeClean = true;
    } else {
      this.isThemeClean = false;
    }
    
    $scope.$watch(() => ([groups.focused()]), ([currentGroup], [prevGroup]) => {
    	if(!prevGroup) {
    		prevGroup = {id: null};
    	}
    	
    	if(!currentGroup) {
    		currentGroup = {id: null};
    	}
    	
    	var group = groups.focused();
    	
    	if(group) {
  			if(group.fromPublicLink) {
      			disableTopbarButtons();
      		} else if(group.permissions) {
      			//$scope.move = group.permissions.edit;
      			$scope.share = group.permissions.share;
      			$scope.groupinfo = group.permissions.view;
      		} 
  			
  			if(!group.objId) {
  				$scope.move = false;
  				$scope.share = false;
  			}
  			if(!group.fromPublicLink && group.objOwner) {
  				$scope.move = true;
  			}
  		} else {
  			disableTopbarButtons();
  		}
      	
    }, true);
    
    function enableTopbarButtons() {
		$scope.move = true;
		$scope.share = true;
		$scope.groupinfo = true;
	}
	
	function disableTopbarButtons() {
		$scope.move = false;
		$scope.share = false;
		$scope.groupinfo = false;
	}
  },
  bindings: {
    auth: '<',
    isSidebar: '<',
    onShowAboutPanel: '&',
    onShowHelpPanel: '&',
    onLogin: '&',
    onLogout: '&',
    onShowSignupBtn: '&',
    onSharePage: '&',
    onMoveWebresource: '&',
    onGroupInfo: '&',
    onViewTsDigest: '&',
    onNewGroup:'&',
    onSignUp: '&',
    searchController: '<',
    sortKey: '<',
    sortKeysAvailable: '<',
    onChangeSortKey: '&',
    pendingUpdateCount: '<',
    onApplyPendingUpdates: '&',
  },
  template: require('../templates/top-bar.html'),
};
