'use strict';


//@ngInject
function MaitenaceSchedulerNtfController($rootScope, $scope, maintenance, $timeout, $compile) {
	var self = this;
	var timer;
	var timer1;

	self.text = "";
	self.title = "Scheduled Maintenance";
	self.tab = 1;
	self.maintenanceScheduleMsg = "";
	self.shutdownNtfMsg = false;
	self.setTab = function(newTab) {
		self.tab = newTab;
	};

	self.isSet = function(tabNum) {
		return self.tab === tabNum;
	};


	$scope.$watch('vm.maintenanceSchedule', function(maintenanceSchedule) {
		if (maintenanceSchedule) {
			init();
		}
	});
	
	$scope.$watch('vm.auth', function(auth) {
		self.auth = auth;
	});

    $scope.$on("$destroy", function () {
	   $timeout.cancel(timer);
	   $timeout.cancel(timer1);
	});


    function handleShutdown() {
		if (!maintenance.isUserRoleAllowed(self.auth["UserRole"])) {
			if(self.auth.status == 'logged-in' ) {
				self.logout().then(function() {
					self.shutdownNtfMsg = true;
					self.text = maintenance.getShutdownNtfMsg(self.maintenanceSchedule);
				});
			} else {
				self.shutdownNtfMsg = true;
				self.text = maintenance.getShutdownNtfMsg(self.maintenanceSchedule);
			}						
		} else {
			self.onClose();
		}
	
	}
	function init() {
		if (self.maintenanceSchedule) {
			if (self.maintenanceSchedule.notificationPeriod) {
				self.title = "Scheduled Maintenance";
				self.text = maintenance.getMaintenanceNtfMsg(self.maintenanceSchedule);
			}

			if (self.maintenanceSchedule.shutdown) {
				self.title = "Scheduled Shutdown";
				var newLogoutDurationMillis = maintenance.getNewLogoutDurationMillis(self.maintenanceSchedule);
				if (newLogoutDurationMillis > 2000) {
					self.text = maintenance.getInitiatedShutdownNtfMsg(self.maintenanceSchedule, newLogoutDurationMillis);
					timer = $timeout(function() {
						$rootScope.$broadcast('timer-start');
						$timeout.cancel(timer);
					}, 100);

					timer1 = $timeout(function() {
						$rootScope.$broadcast('timer-stop');
						$timeout.cancel(timer1);
							handleShutdown();
						}, newLogoutDurationMillis + 100);
				} else {
					handleShutdown();
				}
			}
		}
	}

	init();
}

module.exports = {
	controller: MaitenaceSchedulerNtfController,
	controllerAs: 'vm',
	bindings: {
		onClose: '&',
		logout: '&',
		onLogin:'&',
		maintenanceSchedule: '=',
		auth: '=',
	},
	template: require('../templates/schedule-notification.tpl.html'),
};
