'use strict';

/**
 * A service for displaying "confirm"  messages.
 */

// @ngInject
function timelog($window, settings, endTime) {
	var uri = 'data:application/vnd.ms-excel;base64,',
		template = '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>',
		base64 = function(s) { return $window.btoa(unescape(encodeURIComponent(s))); },
		format = function(s, c) { return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) };
	var extensionLoadingTime = null;
	var TimeLog = {
		timeLogInfo: [],
		setTimeLogInfo: setTimeLogInfo,
		getTimeLogInfo: getTimeLogInfo,
		clearLog: clearLog,
		tableToExcel: tableToExcel
	}
	function setTimeLogInfo(logInfo) {
		TimeLog.timeLogInfo.push(logInfo);
	}

	function getTimeLogInfo() {
		if(extensionLoadingTime) {
			TimeLog.timeLogInfo.unshift(extensionLoadingTime);
		}
		return TimeLog.timeLogInfo;
	}

	function clearLog() {
		TimeLog.timeLogInfo = [];
	}

	function tableToExcel(tableId, worksheetName) {
		var table = angular.element(document.querySelector(tableId)),
			ctx = { worksheet: worksheetName, table: table.html() },
			href = uri + base64(format(template, ctx));
		return href;
	}

	function init() {
		try {
			var time = endTime - settings.startTime;
			time = (time / 1000);
			if (time >= 0) {
				extensionLoadingTime = {
					'timeStamp': settings.startTime,
					'resp_timeStamp': endTime,
					'requestedUrl': "Loading Side bar",
					'httpStatus': "NA",
					'responseTime': time
				};
				timelog.setTimeLogInfo(timeLog);
			}
		} catch (e) {
			
		}

	}
	init();
	return TimeLog;
}

module.exports = timelog;
