'use strict';
var events = require('../events');

/* @ngInject */
function MoveDocConfirmModalController($scope, $modalInstance,confirmdata,modalOptions) {
	'ngInject';
	var self = this;
	
	self.modalOptions = modalOptions;
	self.moveWithAnnotations = {
		option: "move"
	}
	
	
	self.cancel = function() {
		$modalInstance.dismiss('cancel');
	};
	
	self.ok = function() {
		let moveordelete = Object.assign({},self.moveWithAnnotations);
		$modalInstance.close(moveordelete.option);
	};
	 
    self.disableConfirm = function() {
	    if(self.count) {
			return self.moveWithAnnotations.option ? false : true;
	    }
        return false;
	};
	
	function init() {
		self.link = confirmdata.link;
		//self.count = confirmdata.count;
	}
	
	init();
}

module.exports = MoveDocConfirmModalController;
